import React from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';

import List from './views/List';
import Detail from './views/Detail';

function Studios(props) {
  return (
    <React.Fragment>
      <Route exact path={`${props.match.url}`} component={List} />
      <Route path={`${props.match.url}/:id/detail`} component={Detail} />
    </React.Fragment>
  );
}

// withRouter will pass updated match, location,
// and history props to the wrapped component whenever it renders.
export default withRouter(Studios);
