import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Moment from 'moment';
import Button from 'commonComponents/Form/Button';
import apiConfig from 'config/api';
import axios from 'axios';
import * as globalActions from 'state/actions';
import Loader from 'commonComponents/Loader';
import Modal from 'commonComponents/Modal';
import './style.scss';

class Detail extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;
    this.deleteStudio = this.deleteStudio.bind(this);
    this.showConfirm = this.showConfirm.bind(this);
    this.closeConfirm = this.closeConfirm.bind(this);
    this.getStudioMessages = this.getStudioMessages.bind(this);
    this.deleteMessage = this.deleteMessage.bind(this);

    this.state = {
      studio: [],
      isConfirm: false,
      currentStudioId: '',
      studioMessages: [],
    };
  }

  componentDidMount() {
    // const options = {
    //   method: 'GET',
    //   url: '/admin/estudios',
    // };
    this.props.globalActions.fetchStart();
    axios.get(`/admin/estudio/${this.props.match.params.id}`)
      .then((res) => {
        this.setState({
          studio: res.data.studio,
          clientsQuantity: res.data.clientsQuantity,
          foldersQuantity: res.data.foldersQuantity,
          photosQuantity: res.data.photosQuantity,
        });
        this.props.globalActions.fetchFinish();
      })
      .catch((err) => {
        //console.log('Error: /admin/estudios request');
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });

    this.getStudioMessages();
  }

  deleteStudio(id) {
    this.closeConfirm();
    this.props.globalActions.fetchStart();

    axios.delete(`/admin/estudio/${id}`)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.globalActions.fetchFinish();
        this.props.history.push('/admin/studios');
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.props.globalActions.fetchFinish();
      });
  }

  showConfirm(currentStudioId) {
    this.setState({
      isConfirm: true,
      currentStudioId,
    });
  }

  closeConfirm(e) {
    if (e) e.preventDefault();
    this.setState({
      isConfirm: false,
    });
  }

  getStudioMessages() {
    axios.get(`/admin/get-studio-messages/${this.props.match.params.id}`)
      .then((res) => {
        this.setState({
          studioMessages: res.data,
        });
      })
      .catch((err) => {

      });
  }

  deleteMessage(id) {
    axios.delete(`/admin/delete-message/${id}`)
      .then((res) => {
        this.setState(prevState => ({
          studioMessages: prevState.studioMessages.filter(el => el.id !== id),
        }));
        this.props.globalActions.setNotification('success', res.data.message);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  render() {
    const studioData = this.state.studio.map(el => (
      <tr key={el.id}>
        <td>{el.name}</td>
        <td>{Moment(el.created_at).format('DD-MM-YYYY')}</td>
        <td>{el.email}</td>
        <td>{el.location}</td>
        <td>{this.state.clientsQuantity}</td>
        <td>{this.state.foldersQuantity}</td>
        <td>{this.state.photosQuantity}</td>
        <th className="table-controls">
          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-trash-alt"
            actionParams={el.id}
            onClick={this.showConfirm}
          />
        </th>
      </tr>
    ));

    return (
      <section className="view view--admin-studio">

        <Loader isLoading={this.props.global.isFetching} />

        {this.state.isConfirm && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{ textAlign: 'center' }}>
              <Button
                label="Aceptar"
                actionParams={this.state.currentStudioId}
                onClick={this.deleteStudio}
              />
              &nbsp; &nbsp;
              <Button
                label="Cancelar"
                onClick={this.closeConfirm}
              />
            </div>
          </Modal>
        )}

        <header className="view__heading">
          <h4>Estudios</h4>
        </header>

        <div className="view__content container-fluid">

          <div className="card">
            <div className="card-body">

              <div className="table-responsive">
                <table className="table">
                  <thead>
                  <tr>
                    <th>Nombre</th>
                    <th style={{ minWidth: '150px' }}>Registro</th>
                    <th>Email</th>
                    <th>Ubicacion</th>
                    <th>Clientes</th>
                    <th>Carpetas</th>
                    <th>Fotos</th>
                    <th style={{ minWidth: '150px' }}>Controles</th>
                  </tr>
                  </thead>
                  <tbody>
                    { studioData }
                  </tbody>
                </table>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <h6 className="card-title">Mensajes enviados</h6>

              {this.state.studioMessages.map(el => (
                <div className="message-item">
                  {el.message}

                  <Button
                    btStyle="button--icon icon--blue"
                    icon="fas fa-trash-alt"
                    actionParams={el.id}
                    onClick={this.deleteMessage}
                  />
                </div>
              ))}

            </div>
          </div>


        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Detail));
