import React from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';

import List from './views/List';
import CreateUpdate from './views/CreateUpdate';
import Detail from './views/Detail';
import GooglePicker from './views/GooglePicker';

function Clients(props) {
  return (
    <React.Fragment>
      <Route exact path={`${props.match.url}`} component={List} />
      <Route path={`${props.match.url}/:id/detail`} component={Detail} />
      <Route path={`${props.match.url}/create`} component={CreateUpdate} />
      <Route path={`${props.match.url}/:id/edit`} component={CreateUpdate} />
      <Route path={`${props.match.url}/:id/picker`} component={GooglePicker} />
    </React.Fragment>
  );
}

export default withRouter(Clients);
