import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import apiConfig from 'config/api';
import * as globalActions from 'state/actions';
import Button from 'commonComponents/Form/Button';
import Loader from 'commonComponents/Loader';
import * as localActions from '../../actions';
import './style.scss';

class Checkout extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.redirectToDetail = this.redirectToDetail.bind(this);
    this.historyBack = this.historyBack.bind(this);

    this.addRemoveFavorite = this.addRemoveFavorite.bind(this);
    this.addBasketItem = this.addBasketItem.bind(this);
    this.removeBacketItem = this.removeBacketItem.bind(this);
    this.handleComment = this.handleComment.bind(this);
    this.finishCheckout = this.finishCheckout.bind(this);

    this.state = {

    };

    this.folderId = this.props.match.params.folderId;
  }

  componentDidMount() {

  }

  redirectToDetail(id) {
    this.props.history.push(`/studio/clients/${id}/detail`);
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  addRemoveFavorite(e) {
    if (e) e.preventDefault();

    const photoName = e.currentTarget.dataset.name;
    const photoId = e.currentTarget.dataset.id;
    const isFavorite = e.currentTarget.dataset.favorite;

    const data = {
      parentFolderName: this.folderId,
      photoId,
      photoName,
    };

    if (isFavorite) {
      this.removeBacketItem(data);
    } else {
      this.addBasketItem(data);
    }
  }

  addBasketItem(data) {
    return axios.post(`${apiConfig.endPoint}/cliente/cesta`, data)
      .then(() => {
        this.props.localActions.setBasketItem(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  removeBacketItem(data) {
    return axios.delete(`${apiConfig.endPoint}/cliente/cesta/${data.photoId}`)
      .then(() => {
        this.props.localActions.removeBasketItem(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  handleComment(e) {
    const el = e.target;
    const { name, value } = el;
    const id = el.dataset.id;

    this.props.localActions.addCommentToPhoto(this.folderId, id, name, value);
  }

  finishCheckout(e) {
    if (e) e.preventDefault();
    this.props.history.push(`/client/${this.folderId}/finish-checkout`);
  }

  render() {
    const thumbnailElements = [];

    Object.entries(this.props.clientView.basket[this.folderId]).forEach(
      ([key, value]) => {
        const divStyle = { backgroundImage: `url(https://drive.google.com/thumbnail?authuser=0&sz=w320&id=${key})` };

        const isFavorite = this.props.clientView.basket[this.folderId]
          ? this.props.clientView.basket[this.folderId][key]
            ? 'active'
            : ''
          : '';

        thumbnailElements.push(
          <div className="row">
            <div key={key} className="thumbnails__thumbnail-container col-4 col-sm-4 d-flex justify-content-center">
              <div className="thumbnails__image">
                <button
                  type="button"
                  className="thumbnails__favorite"
                  data-name={value.photo_name}
                  data-id={key}
                  data-favorite={isFavorite}
                  onClick={this.addRemoveFavorite}
                >
                  <i className={`fa fa-heart ${isFavorite}`} aria-hidden="true" />
                </button>
                <Link
                  to="#"
                  className="thumbnails__thumbnail-btn"
                  style={divStyle}
                />
              </div>
            </div>

            <div className="checkout-comments col-8">
              <input
                name="photoComment"
                type="text"
                className="form-control"
                placeholder="Dejar comentario"
                data-id={key}
                onChange={this.handleComment}
              />
            </div>
          </div>,
        );
      },
    );

    return (
      <section className="view view--client-productions">

        <Loader isLoading={this.props.global.isFetching} />

        <div className="view__content container-fluid">

          <div className="card">
            <div className="card-body">

              <div className="thumbnails">
                {thumbnailElements}
              </div>
            </div>
          </div>

          <nav className="view__bottom-navigation">

            <span className="back-btn" onClick={this.historyBack}>
              <i className="fa fa-chevron-circle-left" aria-hidden="true" />
            </span>

            <Button
              type="button"
              btStyle="button--icon-text"
              icon="far fa-arrow-alt-circle-right"
              label={`Enviar a ${this.props.session.userProfile.studioName}`}
              onClick={this.finishCheckout}
              isLoading={this.state.isLoading}
            />

          </nav>
        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
    clientView: state.views.client,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    localActions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Checkout));
