import React from 'react';
import {
  EmailShareButton,
  FacebookShareButton,
  PinterestShareButton,
  TwitterShareButton,
  WhatsappShareButton,
} from 'react-share';

import './style.scss';
import axios from 'axios';
import apiConfig from 'config/api';
import { bindActionCreators } from 'redux';
import * as globalActions from 'state/actions';
import { withRouter } from 'react-router-dom';
import connect from 'react-redux/es/connect/connect';
import onClickOutside from 'react-onclickoutside';

class ShareButtons extends React.Component {
  constructor(props) {
    super(props);

    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.state = {
      storagePath: '',
    };
    this.downloadGallery = this.downloadGallery.bind(this);
  }

  downloadGallery(e) {
    if (e) e.preventDefault();

    let data = {
      folderId: this.props.clientView.selectedFolderId,
      folderName: 'download', // this.props.clientView.selectedFolderName,
    };

    if (this.props.public) {
      data = this.props.folderData;
    }

    this.props.dispatch({
      type: 'START_DOWNLOAD_DRIVE_FOLDER',
    });

    let url = '';
    if (this.props.filter === 'fav') {
      url = `${apiConfig.endPoint}/client/download-favourites`;
    } else {
      url = `${apiConfig.endPoint}/client/download-folder`;
    }

    axios.post(url, data)
      .then((res) => {
        const downloadPath = res.data.storagePath || res.data[0].storagePath;

        this.props.dispatch({
          type: 'SUCCESS_DOWNLOAD_DRIVE_FOLDER',
          url: downloadPath,
        });

        setTimeout(() => {
          document.getElementById('driveDownloadLink').click();
        }, 300);
      })
      .catch((err) => {
        console.log(err);
        this.props.dispatch({
          type: 'ERROR_DOWNLOAD_DRIVE_FOLDER',
        });
      });
  }

  handleClickOutside (e) {
    if (this.props.closeMenu) {
      this.props.closeMenu();
    }
  }

  render() {
    let url = '';
    if (this.props.filter === 'fav') {
      url = `${this.props.url}/fav`;
    } else {
      url = this.props.url;
    }

    return (
      <div
        style={this.props.style}
        className="share-buttons"
      >
        <FacebookShareButton
          url={url}
          className="share-buttons__share-button"
        >
          <i className="fab fa-facebook" />
          Facebook
        </FacebookShareButton>

        <TwitterShareButton
          url={url}
          className="share-buttons__share-button"
        >
          <i className="fab fa-twitter" />
          Twitter
        </TwitterShareButton>

        <PinterestShareButton
          url={url}
          media={url}
          className="share-buttons__share-button"
        >
          <i className="fab fa-pinterest-square" />
          Pinterest
        </PinterestShareButton>

        <WhatsappShareButton
          url={url}
          className="share-buttons__share-button"
        >
          <i className="fab fa-whatsapp-square" />
          WhatsApp
        </WhatsappShareButton>

        <EmailShareButton
          url={url}
          className="share-buttons__share-button"
        >
          <i className="fas fa-envelope-square" />
          Email
        </EmailShareButton>

        {this.props.mode === 'gallery'
          ? (
            <a
              className="share-buttons__share-button share-buttons__share-button--download"
              onClick={this.downloadGallery}
            >
              <i className="fas fa-download" />
              Descargar
            </a>
          )
          : (
            <a
              href={this.props.urlDownload}
              className="share-buttons__share-button share-buttons__share-button--download"
            >
              <i className="fas fa-download" />
              Descargar
            </a>
          )
        }
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    clientView: state.views.client,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(onClickOutside(ShareButtons)));
