import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import apiConfig from 'config/api';
import * as globalActions from 'state/actions';
import Button from 'commonComponents/Form/Button';
import Loader from 'commonComponents/Loader';
import * as localActions from '../../actions';
import Modal from 'commonComponents/Modal';
import './style.scss';

class FinishCheckout extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;
    this.historyBack = this.historyBack.bind(this);
    this.finishCheckout = this.finishCheckout.bind(this);
    this.handleTextArea = this.handleTextArea.bind(this);
    this.showModalConfirmation = this.showModalConfirmation.bind(this);
    this.closeModalConfirmation = this.closeModalConfirmation.bind(this);

    this.state = {
      finalComment: '',
      checkoutComplete: false,
    };

    this.folderId = this.props.match.params.folderId;
  }

  componentDidMount() {

  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  handleTextArea(e) {
    const el = e.target;
    const { name, value } = el;

    this.setState({
      finalComment: value,
    });
  }

  finishCheckout(e) {
    if (e) e.preventDefault();

    const data = {
      generalComment: this.state.finalComment,
      photos: [],
    };

    Object.entries(this.props.clientView.basket[this.folderId]).forEach(
      ([key, value]) => {
        data.photos.push({
          photo_id: key,
          photo_name: value.photoName,
          photo_comment: value.photoComment,
        });
      },
    );

    if (data.photos.length < 1) {
      this.props.globalActions.setNotification('error', 'Necesita seleccionar las fotos que desea enviar');
      return;
    }

    axios.post(`${apiConfig.endPoint}/cliente/pedido`, data)
      .then(() => {
        this.showModalConfirmation();

        axios.get(`${apiConfig.endPoint}/cliente/pedidos`)
          .then(() => {
            console.log('ok');
          })
          .catch((err) => {
            console.log(err);
          });


      })
      .catch((err) => {
        console.log(err);
      });
  }

  showModalConfirmation(e) {
    if (e) e.preventDefault();
    this.setState({
      checkoutComplete: true,
    });
  }

  closeModalConfirmation(e) {
    if (e) e.preventDefault();
    this.setState({
      checkoutComplete: false,
    });
    this.props.history.push('/client');
  }

  render() {
    return (
      <section className="view view--client-finish-checkout">

        {this.state.checkoutComplete && (
          <Modal>
            <h5>
              <i className="far fa-check-circle" style={{ color: '#07df7b' }} />
              &nbsp;
              Pedido enviado correctamente!
            </h5>
            <Button
              type="button"
              btStyle="button--icon-text"
              icon="far fa-arrow-alt-circle-right"
              label="Entendido"
              onClick={this.closeModalConfirmation}
              isLoading={this.state.isLoading}
            />
          </Modal>
        )}

        <Loader isLoading={this.props.global.isFetching} />

        <div className="view__content container-fluid">

          <div className="card">
            <div className="card-body">
              <textarea
                className="form-control"
                onChange={this.handleTextArea}
                value={this.state.finalComment}
                placeholder="Dejar un detalle general opcional"
              />
            </div>
          </div>

          <nav className="view__bottom-navigation">

            <span className="back-btn" onClick={this.historyBack}>
              <i className="fa fa-chevron-circle-left" aria-hidden="true" />
            </span>

            <Button
              type="button"
              btStyle="button--icon-text"
              icon="far fa-arrow-alt-circle-right"
              label={`Finalizar envio a ${this.props.session.userProfile.studioName}`}
              onClick={this.finishCheckout}
              isLoading={this.state.isLoading}
            />

          </nav>
        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
    clientView: state.views.client,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    localActions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(FinishCheckout));
