import * as definitions from './actions';
import { OPEN_GOOGLE_PICKER } from './actions';
import { SET_CURRENT_CLIENT } from './actions';

const initialState = {
  userMessages: [],
  pickerCurrentLocation: '',
  pickerCurrentLocationId: '',
  googlePickerIsVisible: false,
  currentClient: [],
};

export default function phrasalVerbsReducer(state = initialState, action) {
  switch (action.type) {
    case definitions.SET_PICKER_LOCATION:
      return {
        ...state,
        pickerCurrentLocation: action.pickerCurrentLocation,
        pickerCurrentLocationId: action.pickerCurrentLocationId,
      };
    case definitions.CLEAR_PICKER_LOCATION:
      return {
        ...state,
        pickerCurrentLocation: '',
        pickerCurrentLocationId: '',
      };
    case definitions.SET_USER_MESSAGES:
      return {
        ...state,
        userMessages: action.userMessages,
      };

    case definitions.OPEN_GOOGLE_PICKER:
      return {
        ...state,
        googlePickerIsVisible: true,
      };

    case definitions.SET_CURRENT_CLIENT:
      return {
        ...state,
        currentClient: action.currentClient,
      };

    case definitions.REMOVE_CURRENT_CLIENT:
      return {
        ...state,
        currentClient: [],
      };

    case definitions.CLOSE_GOOGLE_PICKER:
      return {
        ...state,
        googlePickerIsVisible: false,
        pickerCurrentLocation: '',
        pickerCurrentLocationId: '',
      };

    case 'SET_USER_MESSAGES_READ':
      return {
        ...state,
        userMessages: state.userMessages.map((el, key) => {
          if (!key) {
            return {
              ...el,
              message_status: 1,
            };
          }
          return el;
        }),
      };
    default:
      return state;
  }
}
