import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

// export const hasRole = (user, roles) =>
//   roles.some(role => user.roles.includes(role));

// <RenderControl permittedRoles={['admin', 'studio']}>

const hasRole = (userRole, permittedRoles) => (permittedRoles.some(role => role === userRole));
class RenderControl extends React.Component {
  render() {
    const { userRole, permittedRoles } = this.props;
    if (hasRole(userRole, permittedRoles)) return this.props.children;
    return null;
  }
}

function mapStateToProps(state) {
  return {
    userRole: state.session.userProfile.role,
  };
}

export default withRouter(connect(mapStateToProps)(RenderControl));
