import React from 'react';
import ReactDOM from 'react-dom';
import './style.scss';

class Modal extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const isTransparent = this.props.transparent ? 'modal-scrim--transparent' : '';
    const modalBoxTransparent = this.props.boxTransparent ? 'modal-box--transparent' : '';
    const scrimLight = this.props.scrimLight ? 'modal-scrim--background-light' : '';
    const node = document.getElementById('modal-portal');

    const ModalBase = (
      <div
        role="presentation"
        onClick={this.props.closePopUp}
        className={`modal-scrim ${isTransparent} ${scrimLight}`}
        data-cancel-scrim-click={this.props.cancelOutsideClick}
      >
        <div className={`${this.props.class} modal-box ${modalBoxTransparent}`}>
          {this.props.children}
        </div>
      </div>
    );

    const ModalGallery = (
      <div
        role="presentation"
        onClick={this.props.closePopUp}
        className={`modal-scrim ${isTransparent} ${scrimLight}`}
        data-cancel-scrim-click={this.props.cancelOutsideClick}
      >
        <button
          className="modal-close-button"
          onClick={this.props.closePopUp}
        >
          <i className="modal-close fas fa-times" aria-hidden="true" />
        </button>
          {this.props.children}
      </div>
    );

    if (this.props.isGalleryPortal) {
      console.log('isGalleryModal');
      return ReactDOM.createPortal(
        ModalGallery,
        node,
      );
    }

    return ModalBase;
  }
}

export default Modal;
