/*
 * action types
 */
export const SET_CLIENT_BASKET = 'SET_CLIENT_BASKET';
export const SET_BASKET_ITEM = 'SET_BASKET_ITEM';
export const REMOVE_BASKET_ITEM = 'REMOVE_BASKET_ITEM';
export const ADD_COMMENT_TO_PHOTO = 'ADD_COMMENT_TO_PHOTO';
export const STORE_SELECTED_FOLDER = 'STORE_SELECTED_FOLDER';
export const DELETE_SELECTED_FOLDER = 'DELETE_SELECTED_FOLDER';
export const STORE_SELECTED_FOLDER_TOTAL = 'STORE_SELECTED_FOLDER_TOTAL';

/*
 * action creators
 */
export function setBasketData(data) {
  return (dispatch) => {
    dispatch({
      type: SET_CLIENT_BASKET,
      basket: data,
    });
    return Promise.resolve();
  };
}

export function setBasketItem(data) {
  return {
    type: SET_BASKET_ITEM,
    data,
  };
}

export function removeBasketItem(data) {
  return {
    type: REMOVE_BASKET_ITEM,
    data,
  };
}

export function storeSelectedFolder(data) {
  return {
    type: STORE_SELECTED_FOLDER,
    selectedFolderName: data.selectedFolderName,
    selectedFolderId: data.selectedFolderId,
  };
}

export function deleteSelectedFolder() {
  return {
    type: DELETE_SELECTED_FOLDER,
    selectedFolderName: '',
    selectedFolderId: '',
  };
}

export function setGalleryTotal(total) {
  return {
    type: STORE_SELECTED_FOLDER_TOTAL,
    selectedFolderTotal: total,
  };
}

export function addCommentToPhoto(folderId, id, field, comment) {
  return {
    type: ADD_COMMENT_TO_PHOTO,
    folderId,
    id,
    field,
    comment,
  };
}
