import React from 'react';

class Payments extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
    this.MlSrc = `${document.location.protocol}//secure.mlstatic.com/mptools/render.js`;
  }

  componentDidMount() {
    // Mercado Libre Button
    (() => {
      const s = document.createElement('script');
      s.type = 'text/javascript';
      s.async = true;
      s.src = this.MlSrc;
      const x = document.getElementsByTagName('script')[0];
      x.parentNode.insertBefore(s, x);
    })();
  }

  // Remove ML script tag
  componentWillUnmount() {
    const MlSrc = document.querySelectorAll(`script[src="${this.MlSrc}"]`);
    if (MlSrc.length) {
      MlSrc[0].remove();
    }
  }


  render() {
    return (
      <section className="view view--studio-clients">

        <header className="view__heading">
          <h4>Pagos</h4>
        </header>

        <a
          mp-mode="dftl"
          href="https://www.mercadopago.com/mla/checkout/start?pref_id=384134490-48edd44a-5bb8-41f1-8fc6-70e4bb0fecda"
          name="MP-payButton"
          className="blue-ar-l-rn-none"
        >
          Pagar
        </a>
      </section>
    );
  }
}

export default Payments;
