import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from 'commonComponents/Form/Button';
import apiConfig from 'config/api';
import axios from 'axios';
import * as localActions from '../../../../actions';
import * as globalActions from 'state/actions';
import Loader from 'commonComponents/Loader';
import Modal from 'commonComponents/Modal';
import LogoDrive from 'assets/images/google-drive.png';
// import './style.scss';

class List extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.redirectToDetail = this.redirectToDetail.bind(this);
    this.redirectToEdit = this.redirectToEdit.bind(this);
    this.deleteClient = this.deleteClient.bind(this);
    this.toggleState = this.toggleState.bind(this);
    this.getStudioStatus = this.getStudioStatus.bind(this);
    this.historyBack = this.historyBack.bind(this);
    this.showConfirm = this.showConfirm.bind(this);
    this.closeConfirm = this.closeConfirm.bind(this);
    this.closeDriveNotice = this.closeDriveNotice.bind(this);
    this.getMessages = this.getMessages.bind(this);
    this.goToNewClientView = this.goToNewClientView.bind(this);
    this.goToDrivePermissions = this.goToDrivePermissions.bind(this);

    this.state = {
      clients: [],
      isConfirm: false,
      isNoticeDrive: false,
      currentClientId: '',
    };
  }

  componentDidMount() {
    this.props.globalActions.fetchStart();

    // const options = {
    //   method: 'GET',
    //   headers: { Authorization: this.props.session.token },
    //   url: `${apiConfig.endPoint}/estudio/clients`,
    // };

    axios.get('/estudio/clients')
      .then((res) => {
        this.setState({
          clients: res.data,
        });
        this.props.globalActions.fetchFinish();
      })
      .catch((err) => {
        console.log(err.response);
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.props.globalActions.fetchFinish();
      });

    this.getStudioStatus();
  }

  deleteClient(data) {
    this.props.globalActions.fetchStart();
    this.closeConfirm();
    axios.delete(`/estudio/cliente/${data.clientId}${data.mode === 'all' ? '?all=true' : ''}`)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.globalActions.fetchFinish();

        this.setState(prevState => ({
          clients: prevState.clients.filter(el => el.id !== data.clientId),
        }));
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.props.globalActions.fetchFinish();
      });
  }

  toggleState(e) {
    if (e) e.preventDefault();
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  getStudioStatus() {
    axios.get('/estudio/status')
      .then((res) => {
        this.props.dispatch({
          type: 'SET_STUDIO_STATUS',
          studioStatus: res.data,
        });
        this.setState({
          isNoticeDrive: !res.data.google_drive,
        });
        this.getMessages();
      }).catch((err) => {
        console.log(err.response);
      });
  }

  redirectToEdit(id) {
    this.props.history.push(`/studio/clients/${id}/edit`);
  }

  redirectToDetail(id) {
    this.props.history.push(`/studio/clients/${id}/detail`);
  }

  showConfirm(currentClientId) {
    this.setState({
      isConfirm: true,
      currentClientId,
    });
  }

  closeConfirm(e) {
    if (e) e.preventDefault();
    this.setState({
      isConfirm: false,
    });
  }

  closeDriveNotice(e) {
    if (e) e.preventDefault();
    this.setState({
      isNoticeDrive: false,
    });
    window.location.href = `${apiConfig.endPoint}/auth/drive?user=${this.props.session.userProfile.userId}`;
  }

  getMessages(e) {
    if (e) e.preventDefault();
    axios.get(`/admin/get-studio-messages/${this.props.session.studioStatus.studio_id}`)
      .then((res) => {
        this.props.actions.setUserMessages(res.data);
      }).catch((err) => {
        console.log(err.response);
      });
  }

  goToNewClientView(e) {
    if (e) e.preventDefault();
    this.props.history.push('/studio/clients/create');
  }

  goToDrivePermissions(e) {
    if (e) e.preventDefault();
    window.location.href = `${apiConfig.endPoint}/auth/drive?user=${this.props.session.userProfile.userId}`;
  }


  render() {
    const studioData = this.state.clients.map(el => (
      <tr key={el.id}>
        <td>{el.name}</td>
        <td>{el.email}</td>
        <td>{el.phone}</td>
        <td>{el.folderQuantity}</td>
        <td>{el.photosQuantity}</td>
        <td className="table-controls">
          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-search"
            actionParams={el.id}
            onClick={this.redirectToDetail}
          />

          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-pencil-alt"
            actionParams={el.id}
            onClick={this.redirectToEdit}
          />

          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-trash-alt"
            actionParams={el.id}
            onClick={this.showConfirm}
          />
        </td>
      </tr>
    ));

    const NewClientButton = () => {
      if (this.props.session.studioStatus) {
        return this.props.session.studioStatus.google_drive
          ? (
            <Button
              type="submit"
              btStyle="button--icon-text"
              icon="far fa-arrow-alt-circle-right"
              label="Nuevo cliente"
              isLoading={this.state.isLoading}
              onClick={this.goToNewClientView}
            />
          )
          : (
            <Button
              type="submit"
              btStyle="button--icon-text"
              icon="far fa-arrow-alt-circle-right"
              label="Dar permisos drive"
              isLoading={this.state.isLoading}
              onClick={this.goToDrivePermissions}
            />);
      }
      return null;
    };

    return (
      <section className="view view--studio-clients">

        <Loader isLoading={this.props.global.isFetching} />

        {this.state.isConfirm && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{
              textAlign: 'center',
              display: 'flex',
              justifyContent: 'center',
            }}
            >
              {/*<Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Borrar cliente"
                floatLeft
                isLoading={this.state.isLoading}
                actionParams={{
                  clientId: this.state.currentClientId,
                  mode: 'client',
                }}
                onClick={this.deleteClient}
              />*/}
              &nbsp; &nbsp;
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Borrar cliente"
                floatLeft
                isLoading={this.state.isLoading}
                actionParams={{
                  clientId: this.state.currentClientId,
                  mode: 'all',
                }}
                onClick={this.deleteClient}
              />
              &nbsp; &nbsp;
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Cancelar"
                floatLeft
                onClick={this.closeConfirm}
              />
            </div>
          </Modal>
        )}

        {this.state.isNoticeDrive && (
          <Modal>
            <img
              width={45}
              src={LogoDrive}
              alt=""
              style={{
                position: 'relative',
                left: 'calc(50% - 45px)',
              }}
            />
            <h6 style={{ textAlign: 'center' }}>
              Recuerde que necesita dar permisos a google drive,
              <br />
              para darle funcionalidad a la aplicacin.
            </h6>

            <br />

            <p>
              - Esto permitira a Photoselectpro hacer lo siguiente: <br />
              - Generar una carpeta llamada photoselect en su unidad de google drive <br />
              - Ver, modificar, crear y eliminar archivos solo dentro de la carpeta photoselect <br />
              - photoselectpro ha sido aprobado como sitio seguro por google. <br />
            </p>

            <br />
            <div style={{ textAlign: 'center' }}>
              <Button
                label="Entendido!"
                onClick={this.closeDriveNotice}
              />
            </div>
          </Modal>
        )}

        <header className="view__heading">
          <h4>Clientes</h4>
          &nbsp;
          &nbsp;
          <NewClientButton />

        </header>

        <div className="view__content container-fluid">

          <div className="card">
            <div className="card-body">

              <div className="table-responsive">
                <table className="table">
                  <thead>
                    <tr>
                      <th style={{ minWidth: '150px' }}>Nombre</th>
                      <th>Email</th>
                      <th>Telefono</th>
                      <th>Galerias</th>
                      <th style={{ minWidth: '140px' }}>Total de fotos</th>
                      <th style={{ minWidth: '150px' }}>Controles</th>
                    </tr>
                  </thead>
                  <tbody>
                    {studioData}
                  </tbody>
                </table>
              </div>
            </div>
          </div>

        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(List));
