import { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import apiConfig from 'config/api';
import axios from 'axios';
import * as globalActions from 'state/actions';
import * as utils from 'utils';

class GooglePicker extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;
    this.historyBack = this.historyBack.bind(this);
    this.onPickerApiLoad = this.onPickerApiLoad.bind(this);
    this.mostrarPicker = this.mostrarPicker.bind(this);
    this.pickerCallback = this.pickerCallback.bind(this);

    this.state = {
    };

    this.googlePicker = utils.loadExternalScript('https://apis.google.com/js/api.js');
    this.picker = null;
  }

  componentDidMount() {

    this.googlePicker.then(() => {

      gapi.load('picker', this.onPickerApiLoad);

    }).catch(err => {
      console.log('Error api');
      console.log(err);
    });
  }

  onPickerApiLoad() {
    const studioToken = this.props.session.pickerToken; //TODO: viene uno vencido de backend
    this.createPicker(studioToken);
  }

  createPicker(studioToken) {
    const developerKey = 'AIzaSyAxhxV4Sk27wld9H9EOwHqa-I3WA3_9ep4';
    const appId = '19558418245';

    // const imagesView = new google.picker.View(google.picker.ViewId.DOCS_IMAGES);
    // imagesView.setMimeTypes('image/png,image/jpeg,image/jpg');

    const imagesView = new google.picker.DocsView(google.picker.ViewId.DOCS_IMAGES).setParent(this.props.parentFolder);

    //const uploadView = new google.picker.DocsUploadView().setIncludeFolders(true);
    const uploadView = new google.picker.DocsUploadView().setParent(this.props.parentFolder);

     this.picker = new google.picker
      .PickerBuilder()
      .enableFeature(google.picker.Feature.MULTISELECT_ENABLED)
      .enableFeature(google.picker.Feature.MINE_ONLY)
      // .enableFeature(google.picker.Feature.NAV_HIDDEN)
      .addView(imagesView)
      .addView(uploadView)
      .setLocale('es')
      .setAppId(appId)
      .setOAuthToken(studioToken)
      .setDeveloperKey(developerKey)
      .setCallback(this.pickerCallback)
      .setTitle(`Cargar imagenes en ${this.props.views.studio.pickerCurrentLocation}`)
      //.setSize(1051, 650)
      //.setRelayUrl(window.location.protocol + '//' + window.location.host)
      //.setOrigin(window.location.protocol + '//' + window.location.host)
      .build();

    this.picker.setVisible(true);
  }

  pickerCallback(data) {
    this.props.pickerCallback(data);
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  mostrarPicker(e) {
    if (e) e.preventDefault();
    this.picker.setVisible(true);
  }

  render() {
    return (null);
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    views: state.views,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(GooglePicker));
