import React from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';

import Clients from './views/Clients';
import Profile from './views/Profile';
import Info from './views/Info';
import Payments from './views/Payments';

function Studio(props) {
  return (
    <React.Fragment>
      <Route exact path={`${props.match.url}`} component={Clients} />
      <Route exact path={`${props.match.url}/info`} component={Info} />
      <Route path={`${props.match.url}/clients`} component={Clients} />
      <Route path={`${props.match.url}/profile`} component={Profile} />
      <Route path={`${props.match.url}/payments`} component={Payments} />
    </React.Fragment>
  );
}
export default withRouter(Studio);
