import React from 'react';
import Button from 'commonComponents/Form/Button';
import './style.scss';
import * as globalDefinitions from 'state/actions';

class Notification extends React.Component {
  constructor(props) {
    super(props);
    this.close = this.close.bind(this);
  }

  componentDidMount() {
    const self = this;
    this.timeoutID = setTimeout(() => {
      self.close();
    }, 3000);
  }

  componentWillUnmount() {
    const self = this;
    try {
      clearTimeout(self.timeoutID);
    } catch (err) {
      console.log(err);
    }
  }

  close() {
    this.props.dispatch({
      type: globalDefinitions.CLEAR_NOTIFICATION,
    });
  }

  render() {
    let title = '';
    let className = '';

    switch (this.props.type) {
      case 'hint':
        title = '';
        className = 'notification--hint';
        break;
      case 'warning':
        title = '';
        className = 'notification--warning';
        break;
      case 'success':
        title = '';
        className = 'notification--success';
        break;
      case 'error':
        title = '';
        className = 'notification--error';
        break;
      default:
        title = 'Aviso: ';
    }

    return (
      <div className={`notification ${className}`}>
        <h6>
          <strong>{title}</strong>
          &nbsp;
          {this.props.message}
        </h6>
        <Button
          btStyle="button--icon icon--white"
          icon="far fa-times-circle"
          onClick={this.close}
        />
      </div>
    );
  }
}

export default Notification;
