import React, { Component } from 'react';
import {
  HashRouter, Route,
} from 'react-router-dom';
import { Switch } from 'react-router';
import { Provider } from 'react-redux';
import { PersistGate } from 'redux-persist/integration/react';
// import createHistory from 'history/createBrowserHistory'; // History
import AccessControl from 'commonComponents/AccessControl';
import storeFactory from './state/store';
const { store, persistor } = storeFactory();

/*
  Components Layout
*/
import AppLayout from './layouts/App';
import LandingLayout from './layouts/Landing';

/*
  Components
*/
import FadingRoute from './components/FadingRoute';
import Login from './views/Login';
import Logout from 'commonComponents/Logout';
import Register from './views/Register';

// Views
import Admin from './views/Admin';
import Studio from './views/Studio';
import Client from './views/Client';
import PrivacyPolicies from './views/PrivacyPolicies';
import PublicGallery from './views/Client/views/Gallery/public';

// const history = createHistory();
class Router extends Component {
  render() {
    return (
      <Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
          <HashRouter>
            <Switch>
              <FadingRoute exact path="/" layout={LandingLayout} component={Login} />
              <Route path="/logout" render={() => <Logout store={store} persistor={persistor} />} />
              <FadingRoute path="/register" layout={LandingLayout} component={Register} />
              <FadingRoute path="/admin" layout={AppLayout} component={AccessControl('admin', Admin)} />
              <FadingRoute path="/studio" layout={AppLayout} component={AccessControl('studio', Studio)} />
              <FadingRoute path="/client" layout={AppLayout} component={AccessControl('client', Client)} />
              <FadingRoute path="/galeria/:folderId/:userId/:favorites?" layout={AppLayout} component={PublicGallery} />
              <Route path="/privacy-policies" component={PrivacyPolicies} />
            </Switch>
          </HashRouter>
        </PersistGate>
      </Provider>
    );
  }
}

export default Router;
