import React from 'react';
import './style.scss';

class Button extends React.Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  onClick(e) {
    if (e) e.preventDefault();
    const { actionParams } = this.props;
    if (actionParams) {
      this.props.onClick(actionParams);
      return;
    }
    this.props.onClick();
  }

  render() {
    const label = this.props.isLoading
      ? [<i className="fa fa-refresh fa-spin" />, this.props.statusLabel]
      : this.props.label;

    const isDisabled = this.props.isDisabled || this.props.isLoading;

    let className = this.props.btStyle || '';
    if (this.props.expanded) className += ' button--expanded';
    if (this.props.floatLeft) className += ' float-left';

    let buttonContent = label;

    // Button icon
    if (className.startsWith('button--icon')) {
      buttonContent = <i className={this.props.icon} />;
    }

    // Buton Icon + text
    if (className.startsWith('button--icon-text')) {
      const icon = this.props.isLoading
        ? <i className="fas fa-spinner fa-spin" />
        : <i className={this.props.icon} />;

      if (this.props.isLoading) {
        buttonContent = [icon, this.props.statusLabel];
      } else {
        buttonContent = [icon, this.props.label];
      }
    }

    return (
      <span className="button-component">
        <button
          type={this.props.type || 'button'}
          className={`button ${className}`}
          onClick={this.props.onClick && this.onClick}
          disabled={isDisabled}
        >
          {buttonContent}
        </button>
      </span>
    );
  }
}

export default Button;
