import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import apiConfig from 'config/api';
import * as localActions from '../../actions';
import * as globalActions from 'state/actions';
// import Button from 'commonComponents/Form/Button';
import Loader from 'commonComponents/Loader';
import './style.scss';

class Productions extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.redirectToDetail = this.redirectToDetail.bind(this);
    this.historyBack = this.historyBack.bind(this);
    this.redirectToGallery = this.redirectToGallery.bind(this);

    this.state = {
      productions: null,
    };
  }

  componentDidMount() {
    this.props.globalActions.fetchStart();

    axios.get('/cliente/producciones')
      .then((res) => {
        this.setState({
          productions: res.data,
        });
        this.props.globalActions.fetchFinish();
      })
      .catch((err) => {
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });

    this.props.actions.deleteSelectedFolder();
  }

  redirectToDetail(id) {
    this.props.history.push(`/studio/clients/${id}/detail`);
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  redirectToGallery(e) {
    if (e) e.preventDefault();
    const folderId = e.currentTarget.dataset.folder;
    const folderName = e.currentTarget.dataset.folderName;
    this.props.history.push(`/client/gallery/${folderId}`);
    const data = {
      selectedFolderName: folderName,
      selectedFolderId: folderId,
    };
    this.props.actions.storeSelectedFolder(data);
  }

  render() {
    const productionElement = this.state.productions ? this.state.productions.map((el) => {

      // let blockStyle = {
      //   width: '100%',
      //   height: '100vh',
      // };

      // switch (true) {
      //   case (this.state.productions.length > 2):
      //     blockStyle = {
      //       width: 'calc(50% - 10px)',
      //       height: '60vh',
      //       display: 'initial',
      //       margin: '5px',
      //     };
      //     break;
      //
      //   case (this.state.productions.length > 1):
      //     blockStyle = {
      //       width: 'calc(50% - 10px)',
      //       height: '100vh',
      //       display: 'initial',
      //       margin: '5px',
      //     };
      //     break;
      //
      //   case (this.state.productions.length === 1):
      //     blockStyle = {
      //       width: 'calc(100% - 10px)',
      //       height: '100vh',
      //       margin: '5px',
      //     };
      //     break;
      // }

      const style = {
        backgroundImage: `url(https://drive.google.com/thumbnail?authuser=0&sz=w1000&id=${el.first_photo.id})`,
        // ...blockStyle,
      };

      return (
        <button
          key={el.first_photo.id}
          type="button"
          className="production"
          data-folder={el.folder_id}
          data-folder-name={el.folder_name}
          onClick={this.redirectToGallery}
        >
          <img src={`https://drive.google.com/thumbnail?authuser=0&sz=w1000&id=${el.first_photo.id}`} alt="" />
          <div className="production__title-wrapper">
            <h6 className="production__title">
              {el.folder_name}
            </h6>
          </div>
        </button>
      );
    }) : null;

    return (
      <section className="view view--client-productions">

        <Loader isLoading={this.props.global.isFetching} />

        <header className="view__heading">
          <h4>{`${this.props.session.userProfile.studioName} / ${this.props.session.userProfile.name}`}</h4>
        </header>

        <div className="productions">

          {this.state.productions === null
            ? null
            : this.state.productions.length
              ? productionElement
              : <h5>&nbsp; El fotgrafo an no subio ninguna foto</h5>
          }

        </div>

      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Productions));
