import React, { Component } from 'react';
import {
  withRouter,
  Link,
} from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as localActions from './actions';
import * as globalActions from 'state/actions';
import Notification from 'commonComponents/Notification';
import Button from 'commonComponents/Form/Button';
import Modal from 'commonComponents/Modal';
import Logo from 'assets/images/photoselectpro.png';
import apiConfig from 'config/api';
import axios from 'axios/index';
import jwtDecode from 'jwt-decode';
import { setCustomValidity, clearCustomValidity } from 'utils';
import moment from 'moment';
import './style.scss';

class Login extends Component {
  constructor(props) {
    super(props);
    this.login = this.login.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.getPickerToken = this.getPickerToken.bind(this);
    this.isAuthenticated = this.isAuthenticated.bind(this);
    this.restorePassword = this.restorePassword.bind(this);
    this.togglePasswordRecovery = this.togglePasswordRecovery.bind(this);
    this.closePasswordModal = this.closePasswordModal.bind(this);

    this.state = {
      userName: '',
      password: '',
      isLoading: false,
      accountEmail: '',
      isPasswordModal: false,
    };

    // Redirect authenticated users to their dashboards
    if (this.isAuthenticated()) {
      const { history, session } = this.props;
      const userRole = session.userProfile.role;
      switch (userRole) {
        case 'admin':
          history.push('/admin');
          break;
        case 'studio':
          history.push('/studio/clients');
          break;
        case 'client':
          history.push('/client');
          break;
        default:
          return null;
      }
    }

  }

  handleInput(e) {
    e.preventDefault();
    const element = e.target;
    const inputName = element.name;
    const inputValue = element.value;

    this.setState({
      [inputName]: inputValue,
    });
  }

  login(e) {
    if (e) e.preventDefault();

    this.setState({ isLoading: true });
    const data = {
      userName: this.state.userName,
      password: this.state.password,
    };

    axios.post(`${apiConfig.endPoint}/login`, data)
      .then((res) => {
        const { token } = res.data;
        const payload = jwtDecode(token);
        const userRole = payload.role;
        const { actions, history } = this.props;

        actions.loginSuccess(token).then(() => {
          switch (userRole) {
            case 'admin':
              history.push('/admin');
              break;
            case 'studio':
              this.getPickerToken(token);
              history.push('/studio/clients');
              break;
            case 'client':
              history.push('/client');
              break;
            default:
              return null;
          }
        });
      }).catch((err) => {
        console.log(err.response);
        this.setState({ isLoading: false });
        this.props.globalActions.setNotification('error', err.response.data.message);
      });
  }

  getPickerToken(jwtToken) {
    const { actions } = this.props;

    const options = {
      method: 'GET',
      headers: { Authorization: jwtToken },
      url: `${apiConfig.endPoint}/api/drive/get-fresh-token`,
    };
    axios(options).then(res => {
      const token = res.data.access_token;
      actions.setPickerToken(token); // Store in session
    }).catch(err => (console.log(err)));
  }

  isAuthenticated() {
    const { token, exp } = this.props.session;
    if (token) return !moment.unix(exp).isBefore(new Date());
    return false;
  }

  restorePassword(e) {
    if (e) e.preventDefault();

    const data = {
      email: this.state.accountEmail,
    };

    const options = {
      method: 'POST',
      url: `${apiConfig.endPoint}/reset-password`,
      data,
    };

    axios(options, data)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.closePasswordModal();
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.closePasswordModal();
        console.log(err.response.data.message);
      });
  }

  togglePasswordRecovery(e) {
    if (e) e.preventDefault();

    this.setState(prevState => ({
      isPasswordModal: !prevState.isPasswordModal,
    }));
  }

  closePasswordModal(e) {
    if (e) {
      e.preventDefault();
      const element = e.target;
      if (element.className.startsWith('modal-scrim')) {
        this.setState({
          isPasswordModal: false,
          accountEmail: '',
        });
      }
      return;
    }

    this.setState({
      isPasswordModal: false,
    });
  }

  render() {
    // if (this.state.redirectTo) {
    //   return (<Redirect push to={{ pathname: this.state.redirectPath }} />);
    // }

    return (
      <section className="view view--login">

        {this.props.global.message
        && (
          <Notification
            type={this.props.global.notificationType}
            message={this.props.global.message}
            dispatch={this.props.dispatch}
          />
        )}

        {this.state.isPasswordModal
        && (
          <Modal closePopUp={this.closePasswordModal}>
            <h5>Restaure su contrasea</h5>
              <div className="form-group">
                <label htmlFor="exampleInputEmail1">Email</label>
                <input
                  name="accountEmail"
                  type="text"
                  className="form-control"
                  placeholder="Ingrese su email"
                  onChange={this.handleInput}
                  value={this.state.accountEmail}
                  required
                  onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                  onInput={clearCustomValidity}
                />
              </div>

            <Button
              type="button"
              btStyle="button--default"
              label="enviar"
              onClick={this.restorePassword}
            />
          </Modal>
        )}

        <header className="view__heading">
          <Link to="/">
            <figure className="view__logo">
              <img src={Logo} alt="PhotoSelect logo" />
            </figure>
          </Link>
        </header>

        <div className="view__content">
          <div className="card">
            <div className="card-body">

              <h5 className="card-title">Login</h5>

              <form onSubmit={this.login}>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Username</label>
                  <input
                    name="userName"
                    type="text"
                    className="form-control"
                    placeholder="Username"
                    onChange={this.handleInput}
                    value={this.state.userName}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Password</label>
                  <input
                    name="password"
                    type="password"
                    className="form-control"
                    placeholder="Password"
                    onChange={this.handleInput}
                    value={this.state.password}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="login-box-bottom">
                  <Button
                    type="submit"
                    btStyle="button--icon-text"
                    icon="far fa-arrow-alt-circle-right"
                    label="Ingresar"
                    isLoading={this.state.isLoading}
                  />

                  <Button
                    type="button"
                    btStyle="button--transparent"
                    label="Olvido su password?"
                    onClick={this.togglePasswordRecovery}
                  />
                </div>

              </form>
            </div>
          </div>
        </div>

        <footer className="view__footer">
          Estudio de fotografa?
          &nbsp;
          <Link to="/register">Registro</Link>
        </footer>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    global: state.global,
    views: state.views,
    session: state.session,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));
