import React from 'react';
import { Redirect } from 'react-router-dom';

class Logout extends React.Component {
  componentWillMount() {
    this.logoutUser();
  }

  logoutUser() {
    if (this.props.persistor) {
      this.props.persistor.purge();
      this.props.store.dispatch({
        type: 'LOGOUT',
      });
    }
  }

  render() {
    return (
      <Redirect to="/" />
    );
  }
}

export default Logout;
