/*
 * action types
 */

// Global
export const DATA_FETCH_START = 'DATA_FETCH_START';
export const DATA_FETCH_FINISH = 'DATA_FETCH_SUCCESS';
export const SET_NOTIFICATION = 'SET_NOTIFICATION';
export const CLEAR_NOTIFICATION = 'CLEAR_NOTIFICATION';
// Session
export const LOG_OUT = 'LOG_OUT';
// Location
export const SET_USER_LOCATION = 'SET_USER_LOCATION';
export const DELETE_USER_LOCATION = 'DELETE_USER_LOCATION';

/*
 * action creators
 */
export function setNotification(type, message) {
  return {
    type: SET_NOTIFICATION,
    notificationType: type,
    message,
  };
}

export function fetchStart() {
  return {
    type: DATA_FETCH_START,
    isFetching: true,
  };
}

export function fetchFinish() {
  return {
    type: DATA_FETCH_FINISH,
    isFetching: false,
  };
}
