import React from 'react';
import './style.scss';

class Index extends React.Component {
  render() {
    return (
      this.props.isLoading
        ? (
          <span className="loader-wrapper">
            <span className="loader">
              <span className="loader-inner" />
            </span>
          </span>
        ) : null
    );
  }
}

export default Index;
