import React from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';
import ErrorBoundary from 'commonComponents/ErrorBoundary';
import posed, { PoseGroup } from 'react-pose';

const FadeProps = { // Animation rules for div element
  enter: {
    opacity: 1,
    delay: 280,
    beforeChildren: true,
  },
  exit: { opacity: 0 },
};
const FadeContainer = posed.div(FadeProps);

class FidingRoute extends React.Component {
  render() {
    const {
      component: Component,
      layout: Layout,
      ...rest
    } = this.props;

    return (
      <Layout>
        <PoseGroup>
          <FadeContainer
            className="fade-container"
            key={rest.path}
          >
            <Route
              {...rest}
              render={props => (
                <ErrorBoundary>
                  <Component {...props} />
                </ErrorBoundary>
              )}
            />
          </FadeContainer>
        </PoseGroup>
      </Layout>
    );
  }
}

export default withRouter(FidingRoute);
