import React, { Component } from 'react';
import {
  withRouter,
} from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'commonComponents/Form/Button';
import axios from 'axios/index';
import apiConfig from 'config/api';
import * as globalActions from 'state/actions';
import { clearCustomValidity, setCustomValidity } from 'utils';

class Edit extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.updateUser = this.updateUser.bind(this);
    this.handleInput = this.handleInput.bind(this);

    this.state = {
      userData: {
        name: '',
        email: '',
        phone: '',
        location: '',
        // password: '',
        // confirmPassword: '',
      },
    };

    this.form = React.createRef();
    this.confirmPassword = React.createRef();
  }

  componentDidMount() {
    axios.get(`${apiConfig.endPoint}/estudio/profile`)
      .then((res) => {
        this.setState(prevState => ({
          userData: {
            ...prevState.userData,
            name: res.data.name,
            email: res.data.email,
            phone: res.data.phone,
            location: res.data.location,
          },
        }));
      })
      .catch((err) => {
        console.log(err);
      });
  }

  handleInput(e) {
    e.preventDefault();
    const element = e.target;
    const inputName = element.name;
    const inputValue = element.value;

    this.setState(prevState => ({
      userData: {
        ...prevState.userData,
        [inputName]: inputValue,
      },
    }));
  }

  updateUser(e) {
    if (e) e.preventDefault();
    const data = this.state.userData;

    // No poner validaciones al input directamente como: required, minLenght etc.
    // Mostraria textos en ingles independientemente del customValidity
    if (data.password) {
      if (data.password !== data.confirmPassword) {
        this.confirmPassword.current.focus();
        this.confirmPassword.current.setCustomValidity('El password no concuerda');
        this.form.current.reportValidity();
        return;
      }
    }

    this.props.globalActions.fetchStart();

    axios.put(`${apiConfig.endPoint}/estudio/edit-profile`, data)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message || 'Editado');
        this.props.globalActions.fetchFinish();
        this.props.history.push('/studio');
      })
      .catch(() => {
        this.props.globalActions.setNotification('error', 'Error al intentar editar el perfil');
        this.props.globalActions.fetchFinish();
      });
  }

  render() {
    const { name, email, phone, location, password, confirmPassword } = this.state.userData;

    return (
      <section className="view view--studio-clients">

        <header className="view__heading">
          <h4>Editar perfil</h4>
        </header>

        <div className="view__content">
          <div className="card">
            <div className="card-body">

              <h5 className="card-title">Datos personales</h5>

              <form onSubmit={this.updateUser} ref={this.form}>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Nombre</label>
                  <input
                    name="name"
                    type="text"
                    className="form-control"
                    placeholder="name"
                    onChange={this.handleInput}
                    value={name}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Nueva password</label>
                  <input
                    name="password"
                    type="password"
                    className="form-control"
                    placeholder="Password"
                    onChange={this.handleInput}
                    value={password}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Confirmar Password</label>
                  <input
                    ref={this.confirmPassword}
                    name="confirmPassword"
                    type="password"
                    className="form-control"
                    placeholder="Confirmar password"
                    onChange={this.handleInput}
                    value={confirmPassword}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Email</label>
                  <input
                    name="email"
                    type="email"
                    className="form-control"
                    placeholder="Username"
                    onChange={this.handleInput}
                    value={email}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Telefono</label>
                  <input
                    name="phone"
                    type="text"
                    className="form-control"
                    placeholder="Telefono"
                    onChange={this.handleInput}
                    value={phone}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Direccion</label>
                  <input
                    name="location"
                    type="text"
                    className="form-control"
                    placeholder="Direccion"
                    onChange={this.handleInput}
                    value={location}
                  />
                </div>

                <Button
                  type="submit"
                  btStyle="button--icon-text"
                  icon="far fa-arrow-alt-circle-right"
                  label="Enviar"
                  onClick={this.goToCheckout}
                  isLoading={this.state.isLoading}
                />

              </form>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Edit));
