import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import lozad from 'lozad'; // Lazy image load
import apiConfig from 'config/api';
import * as localActions from '../../actions';
import * as globalActions from 'state/actions';
import Button from 'commonComponents/Form/Button';
import Modal from 'commonComponents/Modal';
import SliderModal from '../SliderModal';
import './style.scss';
import ShareButtons from 'commonComponents/ShareButtons';
require('intersection-observer'); // Lazy image load polyfill

class Gallery extends Component {
  constructor(props) {
    super(props);
    const self = this;
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.redirectToDetail = this.redirectToDetail.bind(this);
    this.historyBack = this.historyBack.bind(this);
    this.getProductionImages = this.getProductionImages.bind(this);
    this.addRemoveFavorite = this.addRemoveFavorite.bind(this);
    this.addBasketItem = this.addBasketItem.bind(this);
    this.removeBacketItem = this.removeBacketItem.bind(this);
    this.goToCheckout = this.goToCheckout.bind(this);
    this.processImagesForRender = this.processImagesForRender.bind(this);
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.toggleShareMenu = this.toggleShareMenu.bind(this);
    this.toggleShareGalleryMenu = this.toggleShareGalleryMenu.bind(this);
    this.filterFavorites = this.filterFavorites.bind(this);
    this.goBack = this.goBack.bind(this);
    this.showBasketConfirm = this.showBasketConfirm.bind(this);
    this.closeBasketConfirm = this.closeBasketConfirm.bind(this);
    this.deleteBasket = this.deleteBasket.bind(this);
    this.showCommentModal = this.showCommentModal.bind(this);
    this.handleComment = this.handleComment.bind(this);
    this.saveComment = this.saveComment.bind(this);

    this.state = {
      thumbnails: [],
      mainWidth: '', // Main container width
      modalVisibility: false,
      imageId: '', // Current selected
      imageKey: '', // Current selected
      activeShareMenu: {},
      isShareMenuBottom: false,
      basketConfirm: false,
      modalComment: false,
      currentPhotoCommentId: null,
    };

    this.observer = lozad('.lozad', {
      loaded: (el) => {
        console.log('cargo');
        const imageFadeScrim = el.previousElementSibling; //parentElement //previousElementSibling
        setTimeout(() => {
          imageFadeScrim.classList.add('opacity-cero');
        }, 200);
      },
    });

    this.folderId = this.props.match.params.folderId;
    this.areFavorites = this.props.match.params.favorites;

    const debounce = (func, wait, immediate) => {
      let timeout = '';
      return () => {
        const context = this;
        const args = arguments;
        const later = () => {
          // Este null solo se usa cuando se pone "inmediate"
          // Basicamente cuando se ejecuta el later, permite poner callNow como true de nuevo
          timeout = null;
          if (!immediate) func.apply(context, args);
        };
        // Para cuando esta "inmediate" al principio se pone true al usar (!'') coerce el timeout
        // Pero luego de la llamada queda en false, porque se setea el timeout de abajo
        const callNow = immediate && !timeout;

        clearTimeout(timeout);
        // Al poner un valor numerico, hace que luego coerza el ->
        // callNow y no se pueda llamar hasta que se termine la llamada al timeout y se ponga en null
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
      };
    };

    // Debounce the windows resize function
    window.addEventListener('resize', debounce(() => {
      self.processImagesForRender();
    }, 200, false), false);

  }

  componentDidMount() {
    this.getProductionImages();
    this.getBasket();
  }

  componentDidUpdate(prevProps) {
    if (this.props.match.params.favorites !== prevProps.match.params.favorites) {
      if (this.props.match.params.favorites === 'fav') {
        this.filterFavorites();
      } else {
        this.processImagesForRender(this.state.serverImages);
      }
    }
  }

  getProductionImages(page = 1) {
    // return new Promise((resolve, reject) => {
    //
    // });

    this.props.globalActions.fetchStart();

    const self = this;

    axios.get(`/cliente/produccion?folderId=${this.folderId}&limit=3000&page=${page}`)
      .then((res) => {
        this.setState({
          serverImages: res.data,
        });

        this.props.localActions.setGalleryTotal(res.data.total);

        // Proccesss images for the grid
        self.processImagesForRender(res.data);

        // Filter favorites
        if (this.areFavorites === 'fav') {
          self.filterFavorites();
        }
      })
      .catch((err) => {
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });
  }

  getBasket(e) {
    if (e) e.preventDefault();

    axios.get('/cliente/cesta')
      .then((res) => {
        const normalized = {
          [this.folderId]: {},
        };

        res.data.forEach((el) => {
          if (this.folderId === el.parent_folder_name) {
            normalized[this.folderId][el.photo_id] = {
              photoName: el.photo_name,
              photoComment: el.photo_comment,
            };
          }
        });

        this.props.localActions.setBasketData(normalized);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  processImagesForRender(images = false) {
    const width = window.innerWidth;
    const thumbnailsNode = document.getElementsByClassName('thumbnails')[0]; // Container
    const scrollWidth = window.innerWidth - thumbnailsNode.clientWidth;
    let gridElementWidth = 0;

    if (width < 718) gridElementWidth = (width - scrollWidth) / 2; // '50%'; 2 col
    if (width > 718) gridElementWidth = (width - scrollWidth) / 3; // '33.33%'; 3 col
    if (width > 1378) gridElementWidth = (width - scrollWidth) / 4; // '25%'; 4 col

    let imagesForProcessing = [];
    if (images) {
      imagesForProcessing = images.data;
    } else {
      imagesForProcessing = this.state.thumbnails;
    }

    const processed = imagesForProcessing.length && imagesForProcessing.map((el) => {
      const originalWidth = el.imageMediaMetadata.width;
      const originalHeight = el.imageMediaMetadata.height;
      const newHeight = (originalHeight * gridElementWidth) / originalWidth;

      const figureStyle = {
        width: gridElementWidth,
        height: newHeight,
        //backgroundImage: `url(https://drive.google.com/thumbnail?authuser=0&sz=w${50}&id=${el.id})`,
      };
      //https://drive.google.com/thumbnail?authuser=0&sz=w500&id=1wA8ZL4TqjXnGBB7Yfbn3WsE9FJq46G4J
      //https://lh3.googleusercontent.com/XIO4IHPgat_uR6V20YcK3rXbceuFVkmnAUc4oVq6xRAmXzxLzj6DjJhcQypnDkdpvh3CEyFgWOU=w500
      return {
        ...el,
        figureStyle,
      };
    });

    this.setState(prevState => ({
      thumbnails: processed,
      gridWidth: gridElementWidth,
      mainWidth: width - scrollWidth,
    }), () => {
      this.observer.observe(); // For lazy loading
      this.props.globalActions.fetchFinish();
    });
  }

  redirectToDetail(id) {
    this.props.history.push(`/studio/clients/${id}/detail`);
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  addRemoveFavorite(e) {
    if (e) e.preventDefault();

    const photoName = e.currentTarget.dataset.name;
    const photoId = e.currentTarget.dataset.id;
    const isFavorite = e.currentTarget.dataset.favorite;

    const data = {
      parentFolderName: this.folderId,
      photoId,
      photoName,
    };

    if (isFavorite) {
      this.removeBacketItem(data);
    } else {
      this.addBasketItem(data);
    }
  }

  addBasketItem(data) {
    return axios.post(`${apiConfig.endPoint}/cliente/cesta`, data)
      .then(() => {
        this.props.localActions.setBasketItem(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  removeBacketItem(data) {
    return axios.delete(`${apiConfig.endPoint}/cliente/cesta/${data.photoId}`)
      .then(() => {
        this.props.localActions.removeBasketItem(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  goToCheckout(e) {
    if (e) e.preventDefault();

    if (this.props.match.params.favorites === 'fav') {
      this.props.history.push(`/client/${this.folderId}/finish-checkout`);
    } else {
      this.props.history.push(`/client/gallery/${this.folderId}/fav`);
    }
  }

  showModal(e) {
    if (e) e.preventDefault();

    const { imageId, imageKey } = e.currentTarget.dataset;

    this.setState({
      modalVisibility: true,
      imageId,
      imageKey,
    });
  }

  closeModal(e) {
    if (e) {
      e.stopPropagation();
      const element = e.target;
      console.log(element);
      if (element.dataset) {
        if (element.dataset.cancelScrimClick) return;
      }

      if (element.className.startsWith('modal-scrim')) {
        this.setState({
          modalVisibility: false,
          modalComment: false,
        });
      }

      if (element.className.startsWith('modal-close')) {
        this.setState({
          modalVisibility: false,
          modalComment: false,
        });
      }
      return;
    }
    this.setState({
      modalVisibility: false,
      modalComment: false,
    });
  }

  toggleShareMenu(e) {
    if (e) {
      e.preventDefault();
      const element = e.currentTarget;
      const id = element.dataset.id;

      this.setState(prevState => ({
        activeShareMenu: {
          [id]: !prevState.activeShareMenu[id],
        },
      }));
    } else {
      // this.setState({
      //   activeShareMenu: {},
      // });
    }
  }

  toggleShareGalleryMenu(e) {
    if (e) e.preventDefault();

    this.setState(prevState => ({
      isShareMenuBottom: !prevState.isShareMenuBottom,
    }));
  }

  filterFavorites(e) {
    if (e) e.preventDefault();
    const selectedFolderId = this.props.clientView.selectedFolderId;
    const basket = this.props.clientView.basket[selectedFolderId];
    const basketKeys = Object.keys(basket);
    const favorites = { data: this.state.thumbnails.filter(el => basketKeys.some(elem => elem === el.id)) };
    this.processImagesForRender(favorites, 'favorites');
  }

  goBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  deleteBasket(e) {
    if (e) e.preventDefault();
    this.closeBasketConfirm();
    return axios.delete(`${apiConfig.endPoint}/cliente/cesta`)
      .then((res) => {
        this.props.localActions.setBasketData({
          [this.folderId]: {},
        });
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.history.push(`/client/gallery/${this.folderId}`);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  showBasketConfirm(e) {
    if (e) e.preventDefault();
    this.setState({
      basketConfirm: true,
    });
  }

  closeBasketConfirm(e) {
    if (e) e.preventDefault();
    this.setState({
      basketConfirm: false,
    });
  }

  showCommentModal(e) {
    const currentPhotoCommentId = e.currentTarget.dataset.photoId;

    if (e) e.preventDefault();
    this.setState({
      modalComment: true,
      currentPhotoCommentId,
    });
  }

  handleComment(e) {
    if (e) e.preventDefault();
    const el = e.target;
    const id = this.state.currentPhotoCommentId;
    const { name, value } = el;

    this.props.localActions.addCommentToPhoto(this.folderId, id, name, value);
  }

  saveComment(photoId) {
    const data = {
      photoId,
      photoComment: this.props.clientView.basket[this.folderId][photoId].photoComment,
    };

    return axios.put('/cliente/cesta', data)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.closeModal();
      })
      .catch((err) => {
        console.log(err);
        this.closeModal();
      });
  }

  render() {
    const width = this.state.mainWidth;
    let counter = 1;
    let columns = '';
    const col1 = [];
    const col2 = [];
    const col3 = [];
    const col4 = [];
    if (width < 718) columns = 2;
    if (width > 718) columns = 3;
    if (width > 1378) columns = 4;

    // Thumbnails to render
    this.state.thumbnails.length && this.state.thumbnails.forEach((el, key) => {
      const isFavorite = this.props.clientView.basket[this.folderId]
        ? this.props.clientView.basket[this.folderId][el.id]
          ? 'active'
          : ''
        : '';

      let thumbnailComment = false;

      try {
        if (this.props.clientView.basket[this.folderId]) {
          if (this.props.clientView.basket[this.folderId][el.id]) {
            if (this.props.clientView.basket[this.folderId][el.id].photoComment !== '') {
              thumbnailComment = true;
            }
          }
        }
      } catch (err) {
        console.log(err);
      }

      const element = (
        <figure
          key={el.id}
          style={el.figureStyle}
        >

          <button
            type="button"
            className="thumbnails__favorite"
            data-name={el.name}
            data-id={el.id}
            data-favorite={isFavorite}
            onClick={this.addRemoveFavorite}
            data-tip="Agregar a la seleccin"
            data-for="agregar-a-pedido"
          >
            <span>
              <i className={`fa fa-heart ${isFavorite}`} />
            </span>
            <span className="favorite-tooltip">
              Agregar a la seleccin
            </span>
          </button>

          <button
            className="thumbnails__share"
            data-id={el.id}
            onClick={this.toggleShareMenu}
            data-tip="Compartir"
            data-for="compartir-imagen"
          >
            <span className="gallery-share">
              <i className="fa fa-share" />
            </span>
            {/*<span className="share-tooltip">
              Compartir
            </span>*/}

            <div className="thumbnail-share-container">
              <ShareButtons
                url={`https://drive.google.com/thumbnail?authuser=0&sz=w${640}&id=${el.id}`}
                urlDownload={`https://drive.google.com/uc?id=${el.id}&export=download`}
                style={{
                  right: '35px',
                  top: '8px',
                }}
              />
            </div>

          </button>

          {(this.props.match.params.favorites === 'fav') && (
            <button
              type="button"
              onClick={this.showCommentModal}
              data-photo-id={el.id}
              className={`thumbnails__comment
              ${thumbnailComment ? ' thumbnails__comment--active' : ''}`}
            >
              <span>
                <i className="fas fa-comment" />
              </span>
              <span className="comment-tooltip">
                Dejar un detalle
              </span>
            </button>
          )}

          <span className="thumbnails__name">
            {el.name}
          </span>

          {/*{this.state.activeShareMenu[el.id] && (
            <ShareButtons
              url={`https://drive.google.com/thumbnail?authuser=0&sz=w${640}&id=${el.id}`}
              urlDownload={`https://drive.google.com/uc?id=${el.id}&export=download`}
              style={{
                right: '48px',
                top: '68px',
              }}
            />
          )}*/}

          <button
            className="button-transparent"
            data-image-id={el.id}
            data-image-key={key}
            onClick={this.showModal}
          >
            <div className="image-fade-scrim" />
            <img
              className="lozad"
              data-src={`https://drive.google.com/thumbnail?authuser=0&sz=w${480}&id=${el.id}`}
              alt={el.name}
            />
          </button>
        </figure>
      );

      if (counter === 1) col1.push(element);

      if (counter === 2) {
        col2.push(element);
        if (columns === 2) counter = 0;
      }

      if (counter === 3) {
        col3.push(element);
        if (columns === 3) counter = 0;
      }

      if (counter === 4) {
        col4.push(element);
        counter = 0;
      }
      counter++;
    });

    let bootstrapCols = ''; // Grid of 12-cols
    if (columns === 4) bootstrapCols = 3;
    if (columns === 3) bootstrapCols = 4;
    if (columns === 2) bootstrapCols = 6;

    return (
      <React.Fragment>
        {this.state.modalVisibility && (
          <Modal
            boxTransparent
            closePopUp={this.closeModal}
            isGalleryPortal
          >
            {this.state.thumbnails && (
              <SliderModal
                thumbnails={this.state.thumbnails}
                imageKey={this.state.imageKey}
                imageId={this.state.imageId}
                folderId={this.folderId}
                basket={this.props.clientView.basket}
                accessControl="private"
                showCommentModal={this.showCommentModal}
              />
            )}
          </Modal>
        )}

        {this.state.modalComment && (
          <Modal closePopUp={this.closeModal}>
            <form className="form-messages">
              <textarea
                className="form-control"
                placeholder="Detalle para esta foto"
                name="photoComment"
                value={this.props.clientView.basket[this.folderId][this.state.currentPhotoCommentId].photoComment}
                onChange={this.handleComment}
              />
              <Button
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Guardar"
                isLoading={this.state.isLoading}
                actionParams={this.state.currentPhotoCommentId}
                onClick={this.saveComment}
              />
            </form>
          </Modal>
        )}

        {this.state.basketConfirm && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{ display: 'flex', justifyContent: 'space-around' }}>
              <Button
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Aceptar"
                isLoading={this.state.isLoading}
                onClick={this.deleteBasket}
              />
              &nbsp; &nbsp;
              <Button
                btStyle="button--icon-text"
                icon="fas fa-ban"
                label="Cancelar"
                onClick={this.closeBasketConfirm}
              />
            </div>
          </Modal>
        )}

      <section className="view view--client-gallery">
        <header className="view__heading">

          <h4 style={{ display: 'flex', alignItems: 'center' }}>
            <span>{this.props.clientView.selectedFolderName}</span>
            &nbsp; &nbsp;

            <Button
              type="button"
              btStyle="button--icon-text"
              icon="far fa-trash-alt"
              label="Eliminar seleccin"
              onClick={this.showBasketConfirm}
            />

          </h4>

          {this.props.match.params.favorites === 'fav' && (
            <span className="photo-detail-info">*Desde esta vista podra dejar un detalle (opcional) en cada foto.</span>
          )}

        </header>

        <div className="view__content container-fluid no-padding">

          <div className="thumbnails">
            <div className="row no-gutters">
              <div className={`col-${bootstrapCols}`}>
                {col1}
              </div>

              <div className={`col-${bootstrapCols}`}>
                {col2}
              </div>

              {col3.length ? (
                <div className={`col-${bootstrapCols}`}>
                  {col3}
                </div>
              ) : null}

              {col4.length ? (
                <div className={`col-${bootstrapCols}`}>
                  {col4}
                </div>
              ) : null}
            </div>
          </div>

          {!this.state.modalVisibility && (
            <nav className="view__bottom-navigation">

            <span className="back-btn" onClick={this.goBack}>
              <i className="fa fa-chevron-circle-left" aria-hidden="true" />
            </span>


              <div className="view__sharing-buttons">

                <i className="fas fa-share" />

                <div className="gallery-share-buttons">
                  <ShareButtons
                    mode="gallery"
                    filter={this.areFavorites}
                    url={`${window.location.protocol}//${window.location.host}/#/galeria/${this.props.clientView.selectedFolderId}/${this.props.session.userProfile.userId}`}
                    urlDownload={`https://drive.google.com/uc?id=${123}&export=download`}
                    style={{
                      left: '-48px',
                      bottom: '40px',
                    }}
                    closeMenu={this.toggleShareGalleryMenu}
                  />
                </div>
              </div>

              <Button
                type="button"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label={`Enviar a ${this.props.session.userProfile.studioName}`}
                onClick={this.goToCheckout}
                isLoading={this.state.isLoading}
              />

            </nav>
          )}

        </div>
      </section>
      </React.Fragment>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
    clientView: state.views.client,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    localActions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Gallery));
