import * as definitions from './actions';

const initialState = {
  isFetching: false,
  message: '',
  notificationType: '',
};

export default function globalReducer(state = initialState, action) {
  switch (action.type) {
    case definitions.DATA_FETCH_START:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case definitions.DATA_FETCH_FINISH:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case definitions.SET_NOTIFICATION:
      return {
        ...state,
        message: action.message,
        notificationType: action.notificationType,
      };
    case definitions.CLEAR_NOTIFICATION:
      return {
        ...state,
        message: '',
        notificationType: '',
      };
    default:
      return state;
  }
}
