import * as definitions from './actions';
import { ADD_COMMENT_TO_PHOTO } from './actions';
import { DELETE_SELECTED_FOLDER } from './actions';
import { STORE_SELECTED_FOLDER_TOTAL } from './actions';

const initialState = {
  basket: {},
  downloadFolder: false,
  folderUrl: '',
  selectedFolderTotal: '',
};

export default function phrasalVerbsReducer(state = initialState, action) {

  const basket = { ...state.basket };

  switch (action.type) {
    case definitions.SET_CLIENT_BASKET:
      return {
        ...state,
        basket: action.basket,
      };

    case definitions.SET_BASKET_ITEM:
      return {
        ...state,
        basket: {
          ...state.basket,
          [action.data.parentFolderName]: {
            ...state.basket[action.data.parentFolderName],
            [action.data.photoId]: {
              photoName: action.data.photoName,
              photoComment: '',
            },
          },
        },
      };

    case definitions.REMOVE_BASKET_ITEM:
      delete basket[action.data.parentFolderName][action.data.photoId];
      return {
        ...state,
        basket,
      };

    case definitions.ADD_COMMENT_TO_PHOTO:
      return {
        ...state,
        basket: {
          ...state.basket,
          [action.folderId]: {
            ...state.basket[action.folderId],
            [action.id]: {
              ...state.basket[action.folderId][action.id],
              [action.field]: action.comment,
            },
          },
        },
      };

    case definitions.STORE_SELECTED_FOLDER:
      return {
        ...state,
        selectedFolderName: action.selectedFolderName,
        selectedFolderId: action.selectedFolderId,
      };

    case definitions.DELETE_SELECTED_FOLDER:
      return {
        ...state,
        selectedFolderName: action.selectedFolderName,
        selectedFolderId: action.selectedFolderId,
      };

    case definitions.STORE_SELECTED_FOLDER_TOTAL:
      return {
        ...state,
        selectedFolderTotal: action.selectedFolderTotal,
      };

    case 'START_DOWNLOAD_DRIVE_FOLDER':
      return {
        ...state,
        downloadFolder: true,
      };

    case 'SUCCESS_DOWNLOAD_DRIVE_FOLDER':
      return {
        ...state,
        downloadFolder: false,
        folderUrl: action.url,
      };

    case 'ERROR_DOWNLOAD_DRIVE_FOLDER':
      return {
        ...state,
        downloadFolder: false,
      };

    default:
      return state;
  }
}
