import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import lozad from 'lozad'; // Lazy image load
import apiConfig from 'config/api';
import * as globalActions from 'state/actions';
import * as localActions from '../../actions';
import Modal from 'commonComponents/Modal';
import SliderModal from '../SliderModal';
import ShareButtons from 'commonComponents/ShareButtons';
require('intersection-observer'); // Lazy image load polyfill
import './style.scss';
import Button from 'commonComponents/Form/Button';
// import Button from './index';

class Gallery extends Component {
  constructor(props) {
    super(props);
    const self = this;
    const token = this.props.session.token || '';
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.getProductionImages = this.getProductionImages.bind(this);
    this.processImagesForRender = this.processImagesForRender.bind(this);
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.toggleShareMenu = this.toggleShareMenu.bind(this);
    this.toggleShareGalleryMenu = this.toggleShareGalleryMenu.bind(this);
    this.filterFavorites = this.filterFavorites.bind(this);
    this.deleteImage = this.deleteImage.bind(this);
    this.showDeleteImageModal = this.showDeleteImageModal.bind(this);
    this.closeDeleteImageModal = this.closeDeleteImageModal.bind(this);

    this.state = {
      thumbnails: [],
      mainWidth: '', // Main container width
      modalVisibility: false,
      imageId: '', // Current selected
      imageKey: '', // Current selected
      deleteId: '', // Current selected image for delete
      activeShareMenu: {},
      isShareMenuBottom: false,
      deleteImageModal: false,
    };

    this.observer = lozad('.lozad', {
      loaded: (el) => {
        const imageFadeScrim = el.previousElementSibling; //parentElement //previousElementSibling
        setTimeout(() => {
          imageFadeScrim.classList.add('opacity-cero');
        }, 200);
      },
    });

    this.folderId = this.props.match.params.folderId;
    this.userId = this.props.match.params.userId;
    this.areFavorites = this.props.match.params.favorites;

    const debounce = (func, wait, immediate) => {
      let timeout = '';
      return () => {
        const context = this;
        const args = arguments;
        const later = () => {
          timeout = null;
          if (!immediate) func.apply(context, args);
        };
        const callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
      };
    };

    // Debounce the windows resize function
    window.addEventListener('resize', debounce(() => {
      self.processImagesForRender();
    }, 200, false), false);
  }

  componentDidMount() {
    this.getProductionImages();
    this.getBasket();
  }

  getProductionImages(page = 1) {
    this.props.globalActions.fetchStart();

    const self = this;

    axios.get(`/cliente/produccion-publica?folderId=${this.folderId}&userId=${this.userId}&limit=3000&page=1`)
      .then((res) => {
        self.processImagesForRender(res.data);

        // Filter favorites
        if (this.areFavorites === 'fav') {
          self.filterFavorites();
        }
      })
      .catch((err) => {
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });
  }

  filterFavorites(e) {
    if (e) e.preventDefault();
    const selectedFolderId = this.folderId; //this.props.clientView.selectedFolderId;
    const basket = this.props.clientView.basket[selectedFolderId];
    const basketKeys = Object.keys(basket);
    const favorites = { data: this.state.thumbnails.filter(el => basketKeys.some(elem => elem === el.id)) };
    this.processImagesForRender(favorites, 'favorites');
  }

  getBasket(e) {
    if (e) e.preventDefault();

    axios.get(`/cliente/cesta-publica?userId=${this.userId}`)
      .then((res) => {
        const normalized = {
          [this.folderId]: {},
        };

        res.data.forEach((el) => {
          if (this.folderId === el.parent_folder_name) {
            normalized[this.folderId][el.photo_id] = {
              photoName: el.photo_name,
              photoComment: el.photo_comment,
            };
          }
        });
        this.props.localActions.setBasketData(normalized);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  processImagesForRender(images = false, filter = false) {
    const width = window.innerWidth;
    const thumbnailsNode = document.getElementsByClassName('thumbnails')[0]; // Container
    const scrollWidth = window.innerWidth - thumbnailsNode.clientWidth;
    let gridElementWidth = 0;

    if (width < 718) gridElementWidth = (width - scrollWidth) / 2; // '50%'; 2 col
    if (width > 718) gridElementWidth = (width - scrollWidth) / 3; // '33.33%'; 3 col
    if (width > 1378) gridElementWidth = (width - scrollWidth) / 4; // '25%'; 4 col

    let imagesForProcessing = [];
    if (images) {
      imagesForProcessing = images.data;
    } else {
      imagesForProcessing = this.state.thumbnails;
    }

    const processed = imagesForProcessing.length && imagesForProcessing.map((el) => {
      const originalWidth = el.imageMediaMetadata.width;
      const originalHeight = el.imageMediaMetadata.height;
      const newHeight = (originalHeight * gridElementWidth) / originalWidth;

      const figureStyle = {
        width: gridElementWidth,
        height: newHeight,
      };
      return {
        ...el,
        figureStyle,
      };
    });

    this.setState(prevState => ({
      thumbnails: processed,
      gridWidth: gridElementWidth,
      mainWidth: width - scrollWidth,
    }), () => {
      this.observer.observe(); // For lazy loading
      this.props.globalActions.fetchFinish();
    });
  }

  showModal(e) {
    if (e) e.preventDefault();

    const { imageId, imageKey } = e.currentTarget.dataset;

    this.setState({
      modalVisibility: true,
      imageId,
      imageKey,
    });
  }

  closeModal(e) {
    if (e) {
      e.stopPropagation();
      const element = e.target;
      console.log(element);
      if (element.dataset) {
        if (element.dataset.cancelScrimClick) return;
      }

      if (element.className.startsWith('modal-scrim')) {
        this.setState({
          modalVisibility: false,
        });
      }

      if (element.className.startsWith('modal-close')) {
        this.setState({
          modalVisibility: false,
        });
      }
      return;
    }
    this.setState({
      modalVisibility: false,
    });
  }

  toggleShareMenu(e) {
    if (e) e.preventDefault();

    const element = e.currentTarget;
    const id = element.dataset.id;

    this.setState(prevState => ({
      activeShareMenu: {
        [id]: !prevState.activeShareMenu[id],
      },
    }));
  }

  toggleShareGalleryMenu(e) {
    if (e) e.preventDefault();

    this.setState(prevState => ({
      isShareMenuBottom: !prevState.isShareMenuBottom,
    }));
  }

  deleteImage() {
    this.props.globalActions.fetchStart();
    this.closeDeleteImageModal();
    const imageId = this.state.deleteId;

    return axios.delete(`/estudio/file/${imageId}?parentId=${this.folderId}`)
      .then((res) => {
        this.getProductionImages();
        this.getBasket();
        this.props.globalActions.setNotification('success', res.data.message);

      })
      .catch((err) => {
        console.log(err);
      });
  }

  showDeleteImageModal(e) {
    if (e) e.preventDefault();
    this.setState({
      deleteImageModal: true,
      deleteId: e.currentTarget.dataset.imageId,
    });
  }

  closeDeleteImageModal(e) {
    if (e) e.preventDefault();
    this.setState({
      deleteImageModal: false,
    });
  }

  render() {
    const width = this.state.mainWidth;
    let counter = 1;
    let columns = 4;
    const col1 = [];
    const col2 = [];
    const col3 = [];
    const col4 = [];
    if (width < 718) columns = 2;
    if (width > 718) columns = 3;
    if (width > 1378) columns = 4;

    this.state.thumbnails.length && this.state.thumbnails.forEach((el, key) => {
      const element = (
        <figure
          key={el.id}
          style={el.figureStyle}
        >

          <button
            type="button"
            className="thumbnails__share thumbnails__share--public"
            data-id={el.id}
            onClick={this.toggleShareMenu}
          >
            <span>
              <i className="fa fa-share" />
            </span>
          </button>

          <span className="thumbnails__name">
            {el.name}
          </span>

          {this.props.session.userProfile.role === 'studio' && (
            <button
              type="button"
              className="thumbnails__delete thumbnails__delete--public"
              data-image-id={el.id}
              onClick={this.showDeleteImageModal}
            >
            <span>
              <i className="fa fa-trash" />
            </span>
            </button>
          )}

          {this.state.activeShareMenu[el.id] && (
            <ShareButtons
              url={`https://drive.google.com/thumbnail?authuser=0&sz=w${640}&id=${el.id}`}
              urlDownload={`https://drive.google.com/uc?id=${el.id}&export=download`}
              style={{
                right: '48px',
                top: '20px',
              }}
            />
          )}

          <button
            className="button-transparent"
            data-image-id={el.id}
            data-image-key={key}
            onClick={this.showModal}
          >
            <div className="image-fade-scrim" />
            <img
              className="lozad"
              data-src={`https://drive.google.com/thumbnail?authuser=0&sz=w${480}&id=${el.id}`}
              alt={el.name}
            />
          </button>
        </figure>
      );

      if (counter === 1) col1.push(element);

      if (counter === 2) {
        col2.push(element);
        if (columns === 2) counter = 0;
      }

      if (counter === 3) {
        col3.push(element);
        if (columns === 3) counter = 0;
      }

      if (counter === 4) {
        col4.push(element);
        counter = 0;
      }
      counter++;
    });

    let bootstrapCols = ''; // Grid of 12-cols
    if (columns === 4) bootstrapCols = 3;
    if (columns === 3) bootstrapCols = 4;
    if (columns === 2) bootstrapCols = 6;

    return (
      <React.Fragment>

        {this.state.modalVisibility && (
          <Modal
            boxTransparent
            closePopUp={this.closeModal}
            isGalleryPortal
          >
            {this.state.thumbnails && (
              <SliderModal
                thumbnails={this.state.thumbnails}
                imageKey={this.state.imageKey}
                imageId={this.state.imageId}
                folderId={this.folderId}
                basket={this.props.clientView.basket}
                accessControl="public"
              />
            )}
          </Modal>
        )}

        {this.state.deleteImageModal && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{
              textAlign: 'center',
              display: 'flex',
              justifyContent: 'center',
            }}
            >
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Borrar"
                floatLeft
                isLoading={this.state.isLoading}
                actionParams={{
                  clientId: 234,
                  mode: 'all',
                }}
                onClick={this.deleteImage}
              />

              &nbsp; &nbsp;
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Cancelar"
                floatLeft
                onClick={this.closeDeleteImageModal}
              />
            </div>
          </Modal>
        )}

      <section className="view view--client-gallery">
        <div className="view__content container-fluid no-padding">

          <div className="thumbnails">
            <div className="row no-gutters">
              <div className={`col-${bootstrapCols}`}>
                {col1}
              </div>

              <div className={`col-${bootstrapCols}`}>
                {col2}
              </div>

              {col3.length ? (
                <div className={`col-${bootstrapCols}`}>
                  {col3}
                </div>
              ) : null}

              {col4.length ? (
                <div className={`col-${bootstrapCols}`}>
                  {col4}
                </div>
              ) : null}
            </div>
          </div>

          {!this.state.modalVisibility && (
            <nav className="view__bottom-navigation view__bottom-navigation--public">

              <div className="view__sharing-buttons">
                <i
                  className="fas fa-share"
                  onClick={this.toggleShareGalleryMenu}
                />

                <div className="gallery-share-buttons">
                  <ShareButtons
                    mode="gallery"
                    filter={this.areFavorites}
                    url={`${window.location.protocol}//${window.location.host}/#/galeria/${this.folderId}/${this.props.session.userProfile.userId}`}
                    urlDownload={`https://drive.google.com/uc?id=${123}&export=download`}
                    style={{
                      left: '-48px',
                      bottom: '40px',
                    }}
                    closeMenu={this.toggleShareGalleryMenu}
                    folderData={{
                      folderId: this.folderId,
                      folderName: 'download',
                    }}
                    public
                  />
                </div>
              </div>

            </nav>
          )}

        </div>
      </section>
      </React.Fragment>
    );
  }
}

function mapStateToProps(state) {
  return {
    global: state.global,
    session: state.session,
    clientView: state.views.client,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    localActions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Gallery));
