import React, { Component } from 'react';
import {
  withRouter,
} from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'commonComponents/Form/Button';
import axios from 'axios/index';
import apiConfig from 'config/api';
import * as globalActions from 'state/actions';
import { clearCustomValidity, setCustomValidity } from 'utils';

class CreateUpdate extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.registerUser = this.registerUser.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.updateUser = this.updateUser.bind(this);
    this.createOrUpdate = this.createOrUpdate.bind(this);

    this.state = {
      userData: {
        name: '',
        userName: '',
        email: '',
        phone: '',
        address: '',
        password: '',
        confirmPassword: '',
        userId: this.props.match.params.id,
      },
    };

    this.form = React.createRef();
    this.confirmPassword = React.createRef();
  }

  componentDidMount() {
    const { userId } = this.state.userData;

    if (userId) {
      axios.get(`${apiConfig.endPoint}/estudio/cliente/${userId}`)
        .then((res) => {
          const { user_name: userName, name, email, phone, address } = res.data.client[0];

          const data = {
            userName,
            name,
            email,
            phone,
            address,
          };

          this.setState(prevState => ({
            ...prevState,
            userData: {
              ...prevState.userData,
              ...data,
            },
          }));
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }

  handleInput(e) {
    e.preventDefault();
    const element = e.target;
    const inputName = element.name;
    const inputValue = element.value;

    this.setState(prevState => ({
      userData: {
        ...prevState.userData,
        [inputName]: inputValue,
      },
    }));
  }

  createOrUpdate(e) {
    if (e) e.preventDefault();

    if (this.state.userData.userId) {
      this.updateUser();
    } else {
      this.registerUser();
    }
  }

  updateUser(e) {
    if (e) e.preventDefault();
    const { name, email, phone, address, password, userId } = this.state.userData;

    const data = {
      name,
      email,
      phone,
      address,
    };

    if (password) {
      data.password = password;
    }

    axios.put(`${apiConfig.endPoint}/estudio/cliente/${userId}`, data)
      .then(() => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.globalActions.fetchFinish();
        this.props.history.push('/studio');
      })
      .catch(() => {
        this.props.globalActions.setNotification('error', 'Error al intentar dar de alta el cliente');
        this.props.globalActions.fetchFinish();
      });
  }

  registerUser(e) {
    if (e) e.preventDefault();
    const data = this.state.userData;

    // No poner validaciones al input directamente como: required, minLenght etc.
    // Mostraria textos en ingles independientemente del customValidity
    if (data.password !== data.confirmPassword) {
      this.confirmPassword.current.focus();
      this.confirmPassword.current.setCustomValidity('El password no concuerda');
      this.form.current.reportValidity();
      return;
    }

    this.props.globalActions.fetchStart();

    axios.post(`${apiConfig.endPoint}/estudio/registro-cliente`, data)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.globalActions.fetchFinish();
        this.props.history.push('/studio');
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.props.globalActions.fetchFinish();
      });
  }

  render() {
    const { name, userName, email, phone, address, password, confirmPassword } = this.state.userData;

    return (
      <section className="view view--studio-clients">

        <header className="view__heading">
          <h4>Nuevo cliente</h4>
        </header>

        <div className="view__content">
          <div className="card">
            <div className="card-body">

              <h5 className="card-title">Registro</h5>

              <form onSubmit={this.createOrUpdate} ref={this.form}>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Nombre</label>
                  <input
                    name="name"
                    type="text"
                    className="form-control"
                    placeholder="name"
                    onChange={this.handleInput}
                    value={name}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Username</label>
                  <input
                    name="userName"
                    type="text"
                    className="form-control"
                    placeholder="Username"
                    onChange={this.handleInput}
                    value={userName}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                    disabled={this.state.userData.userId}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Password</label>
                  <input
                    name="password"
                    type="password"
                    className="form-control"
                    placeholder="Password"
                    onChange={this.handleInput}
                    value={password}
                    required={!this.state.userData.userId}
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label>Confirmar Password</label>
                  <input
                    ref={this.confirmPassword}
                    name="confirmPassword"
                    type="password"
                    className="form-control"
                    placeholder="Confirmar password"
                    onChange={this.handleInput}
                    value={confirmPassword}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Email</label>
                  <input
                    name="email"
                    type="email"
                    className="form-control"
                    placeholder="Username"
                    onChange={this.handleInput}
                    value={email}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Telefono</label>
                  <input
                    name="phone"
                    type="text"
                    className="form-control"
                    placeholder="Telefono"
                    onChange={this.handleInput}
                    value={phone}
                  />
                </div>

                <Button
                  type="submit"
                  btStyle="button--icon-text"
                  icon="far fa-arrow-alt-circle-right"
                  label="Enviar"
                  isLoading={this.state.isLoading}
                />

              </form>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CreateUpdate));
