import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import moment from 'moment';
import Logout from 'commonComponents/Logout';
// import * as globalActions from 'state/actions';

// function mapDispatchToProps(dispatch) {
//   return {
//     globalActions: bindActionCreators(globalActions, dispatch),
//     dispatch,
//   };
// }

function mapStateToProps(state) {
  return {
    session: state.session,
  };
}

const AccessControl = (roles, component) => {
  return withRouter(connect(mapStateToProps)(
    class WithAuthorization extends React.Component {

      constructor(props) {
        super(props);
        this.Component = component;
        this.isAuthenticated = this.isAuthenticated.bind(this);
      }

      componentDidMount() {
        console.log(this.isAuthenticated());
      }

      isAuthenticated() {
        const { token, exp } = this.props.session;
        if (token) return !moment.unix(exp).isBefore(new Date());
        return false;
      }

      render() {
        const { role } = this.props.session.userProfile;
        const isAuthenticated = this.isAuthenticated();
        const hasTheRole = role === roles; // Todo: poner que verifique varios roles.

        if (isAuthenticated && hasTheRole) {
          return (
            <this.Component />
          );
        }

        if (isAuthenticated) {
          return (<div>No access</div>);
        }

        // No access
        return (<Logout />);
      }
    },
  ));
};

export default AccessControl;
