/*
 * action types
 */
export const SET_PICKER_LOCATION = 'SET_PICKER_LOCATION';
export const SET_STUDIO_PICKER_TOKEN = 'SET_STUDIO_PICKER_TOKEN';
export const SET_USER_MESSAGES = 'SET_USER_MESSAGES';
export const CLEAR_PICKER_LOCATION = 'CLEAR_PICKER_LOCATION';
export const OPEN_GOOGLE_PICKER = 'OPEN_GOOGLE_PICKER';
export const CLOSE_GOOGLE_PICKER = 'CLOSE_GOOGLE_PICKER';
export const SET_CURRENT_CLIENT = 'SET_CURRENT_CLIENT';
export const REMOVE_CURRENT_CLIENT = 'REMOVE_CURRENT_CLIENT';

/*
 * action creators
 */
export function setPickerLocation(folderName, folderId) {
  return {
    type: SET_PICKER_LOCATION,
    pickerCurrentLocation: folderName,
    pickerCurrentLocationId: folderId,
  };
}

export function clearPickerLocation() {
  return {
    type: CLEAR_PICKER_LOCATION,
  };
}

export function openGooglePicker() {
  return {
    type: OPEN_GOOGLE_PICKER,
  };
}

export function closeGooglePicker() {
  return {
    type: CLOSE_GOOGLE_PICKER,
  };
}

export function setPickerToken(token) {
  return {
    type: SET_STUDIO_PICKER_TOKEN,
    pickerToken: token,
  };
}

export function setUserMessages(messages) {
  return {
    type: SET_USER_MESSAGES,
    userMessages: messages,
  };
}

export function setCurrentClient(data) {
  return {
    type: SET_CURRENT_CLIENT,
    currentClient: data,
  };
}

export function removeCurrentClient() {
  return {
    type: REMOVE_CURRENT_CLIENT,
  };
}
