import React, { Component } from 'react';
import {
  withRouter,
  Link,
} from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'commonComponents/Form/Button';
import Logo from 'assets/images/photoselectpro.png';
import axios from 'axios/index';
import apiConfig from 'config/api';
import { clearCustomValidity, setCustomValidity } from 'utils';
import * as globalActions from 'state/actions';
import * as loginActions from '../Login/actions';

class Register extends Component {
  constructor(props) {
    super(props);
    this.register = this.register.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.login = this.login.bind(this);
    this.getPickerToken = this.getPickerToken.bind(this);

    this.state = {
      userData: {
        name: '',
        userName: '',
        email: '',
        password: '',
        confirmPassword: '',
        isLoading: false,
      },
    };

    this.form = React.createRef();
    this.confirmPassword = React.createRef();
  }

  handleInput(e) {
    e.preventDefault();
    const element = e.target;
    const inputName = element.name;
    const inputValue = element.value;

    this.setState(prevState => ({
      userData: {
        ...prevState.userData,
        [inputName]: inputValue,
      },
    }));
  }

  register(e) {
    if (e) e.preventDefault();
    const data = this.state.userData;

    // No poner validaciones al input directamente como: required, minLenght etc.
    // Mostraria textos en ingles independientemente del customValidity
    if (data.password !== data.confirmPassword) {
      this.confirmPassword.current.focus();
      this.confirmPassword.current.setCustomValidity('El password no concuerda');
      this.form.current.reportValidity();
      return;
    }

    this.setState({ isLoading: true });

    axios.post(`${apiConfig.endPoint}/register`, data)
      .then((res) => {
        this.login();
        this.props.globalActions.setNotification('success', res.data.message || 'Estudio registrado correctamente');
        this.setState({ isLoading: false });
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.setState({ isLoading: false });
      });
  }

  // Refresh the token
  getPickerToken(jwtToken) {
    const { actions } = this.props;

    const options = {
      method: 'GET',
      headers: { Authorization: jwtToken },
      url: `${apiConfig.endPoint}/api/drive/get-fresh-token`,
    };
    axios(options).then(res => {
      const token = res.data.access_token;
      actions.setPickerToken(token); // Store in session
    }).catch(err => (console.log(err)));
  }

  login(e) {
    if (e) e.preventDefault();

    this.setState({ isLoading: true });

    const data = {
      userName: this.state.userData.userName,
      password: this.state.userData.password,
    };

    axios.post(`${apiConfig.endPoint}/login`, data)
      .then((res) => {
        const { token } = res.data;
        const { actions, history } = this.props;

        actions.loginSuccess(token).then(() => {

          console.log('Entra');
          console.log(this.state);

          this.getPickerToken(token);
          history.push('/studio/clients');
        });

      }).catch((err) => {
        console.log(err);
        //console.log(err.response);
        //this.setState({ isLoading: false });
        //this.props.globalActions.setNotification('error', err.response.data.message);
      });
  }

  render() {
    const { name, userName, email, password, confirmPassword } = this.state.userData;

    return (
      <section className="view view--register">

        <header className="view__heading">
          <Link to="/">
            <figure className="view__logo">
              <img src={Logo} alt="PhotoSelect logo" />
            </figure>
          </Link>
        </header>

        <div className="view__content">
          <div className="card">
            <div className="card-body">

              <h5 className="card-title">Registro de estudios</h5>

              <p style={{
                color: '#498ec7',
                fontWeight: '300',
              }}>
                Registrese como estudio de fotografa o fotgrafo independiente.
              </p>

              <form onSubmit={this.register} ref={this.form}>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Nombre del estudio/fotografo</label>
                  <input
                    name="name"
                    type="text"
                    className="form-control"
                    placeholder="name"
                    onChange={this.handleInput}
                    required
                    value={name}
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Username</label>
                  <input
                    name="userName"
                    type="text"
                    className="form-control"
                    placeholder="Username"
                    onChange={this.handleInput}
                    value={userName}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Email</label>
                  <input
                    name="email"
                    type="email"
                    className="form-control"
                    placeholder="Email"
                    onChange={this.handleInput}
                    value={email}
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Password</label>
                  <input
                    name="password"
                    type="password"
                    className="form-control"
                    placeholder="Password"
                    onChange={this.handleInput}
                    value={password}
                    required
                    onInvalid={e => setCustomValidity(e, 'Campo requerido')}
                    onInput={clearCustomValidity}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Confirmar Password</label>
                  <input
                    ref={this.confirmPassword}
                    name="confirmPassword"
                    type="password"
                    className="form-control"
                    placeholder="Confirmar password"
                    onChange={this.handleInput}
                    value={confirmPassword}
                    onInput={clearCustomValidity}
                  />
                </div>

                <Button
                  type="submit"
                  btStyle="button--icon-text"
                  icon="far fa-arrow-alt-circle-right"
                  label="enviar"
                  isLoading={this.state.isLoading}
                />

              </form>

            </div>
          </div>
        </div>

        <footer className="view__footer">
          Ya tenes una cuenta?
          &nbsp;
          <Link to="/">Ingresar</Link>
        </footer>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    global: state.global,
    views: state.views,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(loginActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Register));
