import React from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';

import Edit from './views/Edit';

function Profile(props) {
  return (
    <React.Fragment>
      <Route exact path={`${props.match.url}`} component={Edit} />
      {/*<Route path={`${props.match.url}/:id/detail`} component={Profile} />*/}
    </React.Fragment>
  );
}

export default withRouter(Profile);
