import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from 'commonComponents/Form/Button';
import apiConfig from 'config/api';
import axios from 'axios';
import * as localActions from '../../../../actions';
import * as globalActions from 'state/actions';
import Loader from 'commonComponents/Loader';
import Modal from 'commonComponents/Modal';
import Notification from 'commonComponents/Notification';
import ReactTooltip from 'react-tooltip';
import './style.scss';

class Detail extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.redirectToDetail = this.redirectToDetail.bind(this);
    this.deleteClient = this.deleteClient.bind(this);
    this.toggleState = this.toggleState.bind(this);
    this.historyBack = this.historyBack.bind(this);
    this.getStudioStatus = this.getStudioStatus.bind(this);
    this.getDrivePermission = this.getDrivePermission.bind(this);
    this.getClientRootFolder = this.getClientRootFolder.bind(this);
    this.getClientFolders = this.getClientFolders.bind(this);
    this.createFolder = this.createFolder.bind(this);
    this.getClientData = this.getClientData.bind(this);
    this.handleInputFolder = this.handleInputFolder.bind(this);
    this.openGooglePicker = this.openGooglePicker.bind(this);
    this.toggleUploadImagesModal = this.toggleUploadImagesModal.bind(this);
    this.setPickerLocation = this.setPickerLocation.bind(this);
    this.closeModals = this.closeModals.bind(this);
    this.sincronizeFiles = this.sincronizeFiles.bind(this);
    this.toggleSincronizeModal = this.toggleSincronizeModal.bind(this);
    this.showConfirm = this.showConfirm.bind(this);
    this.closeConfirm = this.closeConfirm.bind(this);
    this.refreshToken = this.refreshToken.bind(this);
    this.showGalleryConfirm = this.showGalleryConfirm.bind(this);
    this.deleteFolder = this.deleteFolder.bind(this);
    this.handleFolderInput = this.handleFolderInput.bind(this);
    this.renameFolder = this.renameFolder.bind(this);
    this.handleFrontCoverName = this.handleFrontCoverName.bind(this);
    this.setCoverName = this.setCoverName.bind(this);

    this.state = {
      isLoading: false,
      studioStatus: {},
      client: [],
      clientFolder: [],
      clientFolders: [],
      newFolderName: '',
      modalFoldersVisible: false,
      sincronizeIsVisible: false,
      folderButtonIsDisabled: false,
      isConfirm: false,
      currentClientId: '',
      folderToDelete: null,
      //clientOrders: [],
    };
  }

  componentDidMount() {
    this.props.globalActions.fetchStart();
    this.getStudioStatus();
    this.getClientData();
    this.getClientFolders();
    this.getClientOrders();
    this.refreshToken();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.global.message === 'Imagenes cargadas con exito') {
      this.getClientFolders();
    }
  }


  refreshToken() {
    const { actions } = this.props;
    axios.get(`${apiConfig.endPoint}/api/drive/get-fresh-token`).then((res) => {
      const token = res.data.access_token;
      actions.setPickerToken(token); // Store in session
    }).catch(err => (console.log(err)));
  }

  handleInputFolder(e) {
    if (e) e.preventDefault();
    const element = e.target;
    const inputName = element.name;
    const inputValue = element.value;

    this.setState({
      [inputName]: inputValue,
    });
  }

  getClientData(e) {
    if (e) e.preventDefault();
    axios.get(`/estudio/cliente/${this.props.match.params.id}`)
      .then((res) => {
        this.setState({
          client: res.data.client,
        });

        this.props.actions.setCurrentClient(res.data.client);
        this.props.globalActions.fetchFinish();
      })
      .catch((err) => {
        //console.log('Error: /admin/estudios request');
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });
  }

  getStudioStatus() {
    axios.get('/estudio/status')
      .then((res) => {
        this.setState({
          studioStatus: res.data,
        });
      })
      .catch((err) => {
        console.log(err.response);
      });
  }

  getClientOrders() {
    axios.get(`estudio/pedidos?clientId=${this.props.match.params.id}`)
      .then((res) => {
        this.setState({
          clientOrders: res.data,
        });
      })
      .catch((err) => {
        console.log(err.response);
      });
  }

  redirectToDetail(id) {
    this.props.history.push(`/estudio/cliente/${id}/detail`);
  }

  deleteClient(data) {
    this.props.globalActions.fetchStart();

    axios.delete(`/estudio/cliente/${data.clientId}${data.mode === 'all' ? '?all=true' : ''}`)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.globalActions.fetchFinish();
        this.props.history.push('/studio');
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.props.globalActions.fetchFinish();
      });
  }

  toggleState(e) {
    if (e) e.preventDefault();
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  getDrivePermission(e) {
    if (e) e.preventDefault();
    axios.get(`/auth/drive?user=${this.props.session.userProfile.userId}`)
      .then((res) => {

      })
      .catch((err) => {

      });
  }

  // Client root folder
  getClientRootFolder(e) {
    if (e) e.preventDefault();
    return new Promise((resolve, reject) => {
      axios.get('api/drive/get-folders') // All folders
        .then((res) => {
          const clientFolder = res.data.filter(el => (el.name === this.state.client[0].user_name)); //Client folder
          this.setState({ clientFolder });
          resolve(clientFolder);
        })
        .catch((err) => {
          console.log(err);
          reject(err);
        });
    });
  }

  getClientFolders(e) {
    if (e) e.preventDefault();
    //this.props.globalActions.fetchStart();

    this.getClientRootFolder()
      .then((folder) => {
        axios.get(`api/drive/get-client-folders?parentFolder=${folder[0].id}`)
          .then((res) => {
            this.setState({
              clientFolders: res.data,
            });
          })
          .catch((err) => {
            console.log(err);
          });
      }).catch((err) => {
        console.log(err);
      });
  }

  createFolder(e) {
    if (e) e.preventDefault();

    this.setState({ isLoading: true });

    const data = {
      parentFolderId: this.state.clientFolder[0].id,
      folderName: this.state.newFolderName,
    };

    axios.post('/api/drive/create-folder', data)
      .then((res) => {
        this.setState({
          isLoading: false,
        });

        this.setPickerLocation({
          folderName: this.state.newFolderName,
          folderId: res.data.data.fileId,
          callbackName: 'openGooglePicker',
        });
        this.getClientFolders();
      })
      .catch((err) => {
        console.log(err);
        this.setState({ isLoading: false });
      });
  }

  // TODO: ahora que cambio el picker podria separar los metodos, hace un modal distinto para sincronizar los folders y no setear el location
  // Set the folder location and trigger a callback when it's done
  setPickerLocation({ folderName, folderId, callbackName }) {

    const stateCallback = callbackName === 'openGooglePicker'
      ? this.openGooglePicker
      : (initiator => () => this.sincronizeFiles(folderId, folderName, initiator))('button');

    this.props.actions.setPickerLocation(folderName, folderId);

    this.setState({
      pickerLocation: {
        folderName,
        folderId,
      },
      folderButtonIsDisabled: callbackName === 'sincronizeFiles', // Boton Modal sincronizar carpetas, para que no se pueda apretar varias veces.
    }, stateCallback());
  }

  openGooglePicker() {
    this.props.actions.openGooglePicker();
    this.setState({
      modalFoldersVisible: false,
    });
  }

  // initiator: por si se llama automaticamente de otro metodo o de un boton
  sincronizeFiles(folderId, folderName, initiator = null) {

    this.closeModals();
    this.setState({ isLoading: true });

    const data = {
      folderId,
      folderName,
      clientUserId: this.state.client[0].user_id,
    };

    axios.post('/api/drive/send-files-to-client', data)
      .then((res) => { // Si no es un boton el que dispara la accion esto no es necesario, por eso el param initiatior.
        this.setState({ isLoading: false });
        if (initiator === 'button') {
          this.closeModals(); // Pongo el boton como activo y evito un render al pedo desde aca
          this.props.globalActions.setNotification('success', res.data.message || 'Imagenes sincronizadas');
        }
      })
      .catch((err) => {
        console.log(err);
        this.setState({ isLoading: false });
      });
  }

  toggleUploadImagesModal(e) {
    if (e) e.preventDefault();
    this.setState(prevState => ({
      modalFoldersVisible: !prevState.modalFoldersVisible,
    }));
  }

  toggleSincronizeModal(e) {
    if (e) e.preventDefault();
    this.setState(prevState => ({
      sincronizeIsVisible: !prevState.sincronizeIsVisible,
    }));
  }

  closeModals(e) {
    if (e) {
      e.stopPropagation();
      const element = e.target;

      if (element.dataset) {
        if (element.dataset.cancelScrimClick) return;
      }

      if (element.className.startsWith('modal-scrim')) {
        this.setState({
          modalFoldersVisible: false,
          sincronizeIsVisible: false,
        });
      }
      return;
    }
    this.setState({
      modalFoldersVisible: false,
      sincronizeIsVisible: false,
      folderButtonIsDisabled: false, // Pongo activo los botones del folder
    });
  }

  showConfirm(currentClientId) {
    this.setState({
      isConfirm: true,
      currentClientId,
    });
  }

  showGalleryConfirm(folderId) {
    this.setState({
      isGalleryConfirm: true,
      folderToDelete: folderId,
    });
  }

  closeConfirm(e) {
    if (e) e.preventDefault();
    this.setState({
      isConfirm: false,
      isGalleryConfirm: false,
    });
  }

  deleteFolder(id) {
    this.setState({ isLoading: true });
    axios.delete(`/estudio/folder/${id}`)
      .then((res) => {
        this.setState(prevState => ({
          isConfirm: false,
          isGalleryConfirm: false,
          isLoading: false,
          clientFolders: prevState.clientFolders.filter(folder => folder.id !== id),
        }));
        this.props.globalActions.setNotification('success', res.data.message);
      })
      .catch((err) => {
        console.log(err);
        this.setState({
          isConfirm: false,
          isGalleryConfirm: false,
          isLoading: false,
        });
        this.props.globalActions.setNotification('error', err.response.data.message);
      });
  }

  handleFolderInput(e) {
    if (e) e.preventDefault();

    const el = e.target;
    const { name, value } = el;
    const folderId = el.dataset.id;

    console.log(value);

    this.setState(prevState => ({
      clientFolders: prevState.clientFolders.map((folder) => {
        if (folder.id === folderId) {
          return {
            ...folder,
            name: value,
          };
        }
        return folder;
      }),
    }));
  }

  renameFolder(folderId) {
    const data = {
      folderId,
    };

    this.state.clientFolders.forEach((folder) => {
      if (folderId === folder.id) {
        data.newFolderName = folder.name;
      }
    });

    axios.put('/api/drive/update-folder', data)
      .then((res) => {
        this.setState({ isLoading: false });
        this.props.globalActions.setNotification('success', res.data.message || 'Carpeta renombrada');
      })
      .catch((err) => {
        console.log(err);
        this.setState({ isLoading: false });
      });
  }

  handleFrontCoverName(e) {
    const el = e.target;
    const { name: folderId, value } = el; // El name es el id del folder

    this.setState(prevState => ({
      clientFolders: prevState.clientFolders.map((folder) => {
        if (folder.id === folderId) {
          return {
            ...folder,
            firstPhoto: value,
          };
        }
        return folder;
      }),
    }));
  }

  setCoverName(folderId) {
    const data = {
      id: folderId,
      firstPhotoName: '',
    };

    this.state.clientFolders.forEach((el) => {
      if (el.id === folderId) {
        data.firstPhotoName = el.firstPhoto;
      }
    });

    axios.put('/estudio/folder', data)
      .then((res) => {
        this.setState({ isLoading: false });
        this.props.globalActions.setNotification('success', res.data.message || 'Todo ok');
      })
      .catch((err) => {
        console.log(err);
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.setState({ isLoading: false });
      });
  }


  render() {
    const clientData = this.state.client.map(el => (
      <tr key={el.id}>
        <td>{el.name}</td>
        <td>{el.email}</td>
        <td>{el.phone}</td>
        <td className="table-controls">
          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-trash-alt"
            actionParams={el.id}
            onClick={this.showConfirm}
          />
        </td>
      </tr>
    ));

    const DriveButton = () => {
      if (this.state.studioStatus.user_id) {
        return this.state.studioStatus.google_drive
          ? (
            <div className="drive-button">
              <Button
                type="submit"
                btStyle="button--icon-text cargar-imagenes-btn"
                icon="far fa-arrow-alt-circle-right"
                label="Cargar Imgenes / Galeras"
                onClick={this.toggleUploadImagesModal}
                floatLeft
              />
              &nbsp;
              <ReactTooltip
                id="optimizar-button"
                place="bottom"
                type="dark"
                effect="float"
              />

              {/*<span
                data-tip="Optimizar ultimas fotos subidas a google drive"
                data-for="optimizar-button"
              >
                <Button
                  type="submit"
                  btStyle="button--icon-text"
                  icon="far fa-arrow-alt-circle-right"
                  label="Optimmizar imagenes para el cliente"
                  statusLabel="Optimizando imagenes"
                  onClick={this.toggleSincronizeModal}
                  isLoading={this.state.isLoading}
                  floatLeft
                />
              </span>*/}

            </div>
          )
          : (
              <a
                href={`${apiConfig.endPoint}/auth/drive?user=${this.props.session.userProfile.userId}`}
                className="btn-link btn-link--title"
              >
                Dar permisos drive
              </a>
          );
      }
      return null;
    };

    const ClientFolders = (props) => {
      const clientFolders = this.state.clientFolders.length ? this.state.clientFolders.map((folder) => {
        return (
          <Button
            type="submit"
            btStyle="button--icon-text"
            icon="far fa-arrow-alt-circle-right"
            label={folder.name}
            actionParams={{ folderName: folder.name, folderId: folder.id, callbackName: props.callbackName }}
            onClick={this.setPickerLocation}
            isDisabled={this.state.folderButtonIsDisabled}
          />
        );
      }) : [];

      return(
        <div className="modal-molders__locations">
          <h5>
            <i className="fas fa-folder-open" /> Seleccione ubicacion
          </h5>
          {clientFolders}
        </div>
      );
    };

    const UploadImagesModal = this.state.modalFoldersVisible ? (
      <Modal
        class="modal-molders"
        closePopUp={this.closeModals}
      >
        {this.state.clientFolders.length ? (
          <ClientFolders callbackName="openGooglePicker" />
        ) : null}

        <div className="modal-molders__new-folder">
          <h5>
            <i className="fas fa-folder-plus" /> Crear una nueva carpeta
          </h5>
          <div className="form-group">
            <input
              name="newFolderName"
              className="form-control"
              type="text"
              onChange={this.handleInputFolder}
              value={this.state.newFolderName}
            />
          </div>
          <Button
            type="submit"
            btStyle="button--icon-text"
            icon="far fa-arrow-alt-circle-right"
            label="Enviar"
            onClick={this.createFolder}
            isLoading={this.state.isLoading}
          />

        </div>
      </Modal>
    ) : null;

    const SincronizeModal = this.state.sincronizeIsVisible ? (
      <Modal
        class="modal-molders"
        closePopUp={this.closeModals}
      >
        <ClientFolders callbackName="sincronizeFiles" />
      </Modal>
    ) : null;

    const clientFolders = this.state.clientFolders.map((el, key) => {
      return (
        <tr>
          <td>
            <a
              href={`${window.location.protocol}//${window.location.host}/#/galeria/${el.id}/${this.state.client[0].user_id}`}
              target="_blank"
              rel="noopener"
            >
              <strong>{el.name}</strong>
            </a>
          </td>
          <td>
            <strong>{el.photoQuantity}</strong>
          </td>
          <td>
            <input
              name="name"
              type="text"
              placeholder="Nuevo nombre"
              onChange={this.handleFolderInput}
              value={el.name}
              data-id={el.id}
            />
            <Button
              btStyle="button--icon icon--blue"
              icon="far fa-arrow-alt-circle-right"
              actionParams={el.id}
              onClick={this.renameFolder}
            />
          </td>
          <td>
            <input
              name={el.id}
              type="text"
              placeholder="Nombre de la imagen"
              onChange={this.handleFrontCoverName}
              value={el.firstPhoto}
            />
            <Button
              btStyle="button--icon icon--blue"
              icon="far fa-arrow-alt-circle-right"
              actionParams={el.id}
              onClick={this.setCoverName}
            />
          </td>
          <td>
            <Button
              btStyle="button--icon icon--blue"
              icon="fas fa-trash-alt"
              actionParams={el.id}
              onClick={this.showGalleryConfirm}
            />
          </td>
        </tr>
      );
    });

    return (
      <section className="view view--studio-clients">

        {UploadImagesModal}
        {SincronizeModal}

        <Loader isLoading={this.props.global.isFetching} />

        {this.state.isConfirm && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{
              textAlign: 'center',
              display: 'flex',
              justifyContent: 'center',
            }}
            >
              {/*<Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Borrar cliente"
                floatLeft
                isLoading={this.state.isLoading}
                actionParams={{
                  clientId: this.state.currentClientId,
                  mode: 'client',
                }}
                onClick={this.deleteClient}
              />*/}
              &nbsp; &nbsp;
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Borrar cliente"
                floatLeft
                isLoading={this.state.isLoading}
                actionParams={{
                  clientId: this.state.currentClientId,
                  mode: 'all',
                }}
                onClick={this.deleteClient}
              />
              &nbsp; &nbsp;
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Cancelar"
                floatLeft
                onClick={this.closeConfirm}
              />
            </div>
          </Modal>
        )}

        {this.state.isGalleryConfirm && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{
              textAlign: 'center',
              display: 'flex',
              justifyContent: 'center',
            }}
            >
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Aceptar"
                floatLeft
                isLoading={this.state.isLoading}
                actionParams={this.state.folderToDelete}
                onClick={this.deleteFolder}
              />
              &nbsp; &nbsp;
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Cancelar"
                floatLeft
                onClick={this.closeConfirm}
              />
            </div>
          </Modal>
        )}

        {this.props.global.message
        && (
          <Notification
            type={this.props.global.notificationType}
            message={this.props.global.message}
            dispatch={this.props.dispatch}
          />
        )}

        <header className="view__heading view__heading--client-detail">

          <h4>
            Detalle del cliente
          </h4>

          <DriveButton />

        </header>

        <div className="view__content container-fluid">

          <div className="card">
            <div className="card-body">

              <div className="table-responsive">
                <table className="table">
                  <thead>
                    <tr>
                      <th style={{ minWidth: '155px' }}>Nombre</th>
                      <th>Email</th>
                      <th>Telefono</th>
                      <th>Controles</th>
                    </tr>
                  </thead>
                  <tbody>
                    { clientData }
                  </tbody>
                </table>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <h4>Galerias</h4>

              <div className="table-responsive table-galleries">
                <table className="table">
                  <thead>
                    <tr>
                      <th style={{ minWidth: '100px' }}>Nombre</th>
                      <th style={{ minWidth: '155px' }}>Cantidad de fotos</th>
                      <th style={{ minWidth: '245px' }}>Renombrar</th>
                      <th style={{ minWidth: '245px' }}>Foto de portada</th>
                      <th>Eliminar</th>
                    </tr>
                  </thead>
                  <tbody>
                    {clientFolders}
                  </tbody>
                </table>
              </div>

            </div>
          </div>

        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Detail));
