import React from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';

import Productions from './views/Productions';
import Checkout from './views/Checkout';
import Gallery from './views/Gallery';
import SliderModal from './views/SliderModal';
import FinishCheckout from './views/FinishCheckout';
import Info from './views/Info';

function Clients(props) {
  return (
    <React.Fragment>
      <Route exact path={`${props.match.url}`} component={Productions} />
      <Route exact path={`${props.match.url}/info`} component={Info} />
      <Route exact path={`${props.match.url}/:folderId/checkout`} component={Checkout} />
      <Route exact path={`${props.match.url}/:folderId/finish-checkout`} component={FinishCheckout} />
      <Route exact path={`${props.match.url}/modal/:mode/:imageId`} component={SliderModal} />
      <Route exact path={`${props.match.url}/gallery/:folderId/:favorites?`} component={Gallery} />
    </React.Fragment>
  );
}
export default withRouter(Clients);
