import React from 'react';
import { Link } from 'react-router-dom';
import RenderControl from 'commonComponents/RenderControl';
import './style.scss';

class SidebarMenu extends React.Component {

  constructor(props) {
    super(props);
  }

  onClick(e) {

  }

  render() {

    return (
      <nav>
        <ul className="sidebar-menu">

          <RenderControl permittedRoles={['admin']}>
            <li className="sidebar-menu__first-items">
              <Link to="/admin">
                <i className="fas fa-home" />
                Inicio
              </Link>
            </li>
            <li className="sidebar-menu__first-items">
              <Link to="/admin/studios">
                <i className="fas fa-store-alt" />
                Estudios
              </Link>
            </li>
          </RenderControl>

          <RenderControl permittedRoles={['studio']}>
            {!this.props.locationPath.startsWith('/galeria') && (
              <li className="sidebar-menu__first-items">
                <Link to="/studio">
                  <i className="fas fa-home" />
                  Inicio
                </Link>
                <Link to="/studio/profile">
                  <i className="fas fa-user" />
                  Perfil
                </Link>
                <Link to="/studio/payments">
                  <i className="fas fa-dollar-sign" />
                  Realizar un pago
                </Link>
              </li>
            )}
          </RenderControl>

        </ul>
      </nav>
    );
  }
}

export default SidebarMenu;
