const initialState = {
  userProfile: {},
  token: null,
  pickerToken: null,
  exp: null,
  // studioStatus: {},
};

export default function sessionReducer(state = initialState, action) {
  switch (action.type) {
    case 'LOG_IN_SUCCESS':
      return {
        ...state,
        userProfile: action.userProfile,
        token: action.token,
        exp: action.exp,
      };
    case 'SET_STUDIO_PICKER_TOKEN':
      return {
        ...state,
        pickerToken: action.pickerToken,
      };
    case 'SET_STUDIO_STATUS':
      return {
        ...state,
        studioStatus: action.studioStatus,
      };
    default:
      return state;
  }
}
