import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import apiConfig from 'config/api';
import * as globalActions from 'state/actions';
import Button from 'commonComponents/Form/Button';
// import * as utils from 'utils';
import Carousel from 'nuka-carousel';
import Hammer from 'hammerjs';
import SpinnerPreloader from 'commonComponents/SpinnerPreloader';
import * as localActions from '../../actions';
import './style.scss';
import ShareButtons from 'commonComponents/ShareButtons';

class SliderModal extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;
    this.folderId = this.props.match.params.folderId;

    // Methods
    this.historyBack = this.historyBack.bind(this);
    this.hammerIt = this.hammerIt.bind(this);
    this.slideFinish = this.slideFinish.bind(this);
    this.goToCheckout = this.goToCheckout.bind(this);
    this.addRemoveFavorite = this.addRemoveFavorite.bind(this);
    this.addBasketItem = this.addBasketItem.bind(this);
    this.removeBacketItem = this.removeBacketItem.bind(this);
    this.toggleShareGalleryMenu = this.toggleShareGalleryMenu.bind(this);

    // TODO: revisar
    let images = [];
    // Less 15 images
    if (this.props.thumbnails.length < 15) {
      images = this.props.thumbnails.map((el, key) => ({
        key,
        id: el.id,
      }));

      this.state = {
        lastScale: 1,
        speed: 0,
        images,
        slideIndex: this.props.imageKey,
      };
      return;
    }

    // More than 15 images
    let slideIndex = 1;
    const { imageId: currentId, thumbnails } = this.props;
    const currentKey = Number(this.props.imageKey);
    const thumbsLength = thumbnails.length - 1; // From 0
    const limitPosition = thumbsLength - currentKey; // 4 first or 4 last
    const isGreater = limitPosition < 10;
    const isLower = currentKey < 10;

    if (isGreater) {
      slideIndex = 10 - limitPosition;
      images = [
        { key: thumbsLength - 10, id: thumbnails[thumbsLength - 10].id },
        { key: thumbsLength - 9, id: thumbnails[thumbsLength - 9].id },
        { key: thumbsLength - 8, id: thumbnails[thumbsLength - 8].id },
        { key: thumbsLength - 7, id: thumbnails[thumbsLength - 7].id },
        { key: thumbsLength - 6, id: thumbnails[thumbsLength - 6].id },
        { key: thumbsLength - 5, id: thumbnails[thumbsLength - 5].id },
        { key: thumbsLength - 4, id: thumbnails[thumbsLength - 4].id },
        { key: thumbsLength - 3, id: thumbnails[thumbsLength - 3].id },
        { key: thumbsLength - 2, id: thumbnails[thumbsLength - 2].id },
        { key: thumbsLength - 1, id: thumbnails[thumbsLength - 1].id },
        { key: thumbsLength, id: thumbnails[thumbsLength].id },
      ];
    }

    if (isLower) {
      slideIndex = currentKey;
      images = [
        { key: 0, id: thumbnails[0].id },
        { key: 1, id: thumbnails[1].id },
        { key: 2, id: thumbnails[2].id },
        { key: 3, id: thumbnails[3].id },
        { key: 4, id: thumbnails[4].id },
        { key: 5, id: thumbnails[5].id },
        { key: 6, id: thumbnails[6].id },
        { key: 7, id: thumbnails[7].id },
        { key: 8, id: thumbnails[8].id },
        { key: 9, id: thumbnails[9].id },
        { key: 10, id: thumbnails[10].id },
      ];
    }

    if (!isLower && !isGreater) {
      slideIndex = 5;
      images = [
        { key: currentKey - 5, id: thumbnails[currentKey - 5].id },
        { key: currentKey - 4, id: thumbnails[currentKey - 4].id },
        { key: currentKey - 3, id: thumbnails[currentKey - 3].id },
        { key: currentKey - 2, id: thumbnails[currentKey - 2].id },
        { key: currentKey - 1, id: thumbnails[currentKey - 1].id },
        { key: currentKey, id: currentId },
        { key: currentKey + 1, id: thumbnails[currentKey + 1].id },
        { key: currentKey + 2, id: thumbnails[currentKey + 2].id },
        { key: currentKey + 3, id: thumbnails[currentKey + 3].id },
        { key: currentKey + 4, id: thumbnails[currentKey + 4].id },
        { key: currentKey + 5, id: thumbnails[currentKey + 5].id },
      ];
    }

    this.state = {
      lastScale: 1,
      speed: 0,
      images,
      slideIndex,
      isShareMenuBottom: false,
    };
  }

  componentDidMount() {
    const elements = document.querySelectorAll('.modal-image');
    elements.forEach((el) => {
      this.hammerIt(el);
    });

    // Esto es para evitar que se note el cambio del slide al principio por poner un slideIndex
    // Al principio esta en 0
    setTimeout(() => {
      this.setState({
        speed: 280,
      });
    }, 100);
  }

  hammerIt(elm) {
    const self = this;

    const hammertime = new Hammer(elm, {});
    hammertime.get('pinch').set({
      enable: true,
    });

    let posX = 0,
      posY = 0,
      scale = 1,
      last_scale = 1,
      last_posX = 0,
      last_posY = 0,
      max_pos_x = 0,
      max_pos_y = 0,
      transform = '',
      el = elm;

    hammertime.on('doubletap pan pinch panend pinchend', function (ev) {
      if (ev.type === 'doubletap') {
        transform =
          'translate3d(0, 0, 0) ' +
          'scale3d(2, 2, 1) ';
        scale = 2;
        last_scale = 2;
        try {
          if (window.getComputedStyle(el, null).getPropertyValue('-webkit-transform').toString() !== 'matrix(1, 0, 0, 1, 0, 0)') {
            transform =
              'translate3d(0, 0, 0) ' +
              'scale3d(1, 1, 1) ';
            scale = 1;
            last_scale = 1;
          }
        } catch (err) {
        }
        el.style.webkitTransform = transform;
        transform = '';
      }

      // pan
      if (scale !== 1) {
        posX = last_posX + ev.deltaX;
        posY = last_posY + ev.deltaY;
        max_pos_x = Math.ceil((scale - 1) * el.clientWidth / 2);
        max_pos_y = Math.ceil((scale - 1) * el.clientHeight / 2);
        if (posX > max_pos_x) {
          posX = max_pos_x;
        }
        if (posX < -max_pos_x) {
          posX = -max_pos_x;
        }
        if (posY > max_pos_y) {
          posY = max_pos_y;
        }
        if (posY < -max_pos_y) {
          posY = -max_pos_y;
        }
      }

      // pinch
      if (ev.type === 'pinch') {
        console.log('pinch');
        scale = Math.max(.999, Math.min(last_scale * (ev.scale), 4));
      }
      if (ev.type === 'pinchend') {
        console.log('pinchend');
        console.log(scale);
        last_scale = scale;

        self.setState({
          lastScale: scale,
        });
      }

      // panend
      if (ev.type === 'panend') {
        last_posX = posX < max_pos_x ? posX : max_pos_x;
        last_posY = posY < max_pos_y ? posY : max_pos_y;
      }

      if (scale !== 1) {
        transform =
          'translate3d(' + posX + 'px,' + posY + 'px, 0) ' +
          'scale3d(' + scale + ', ' + scale + ', 1)';
      }

      if (transform) {
        el.style.webkitTransform = transform;
      }
    });
  }

  historyBack(e) {
    if (e) e.preventDefault();
    this.props.history.goBack();
  }

  slideFinish(slideIndex) {
    if (this.props.thumbnails.length < 15) return;

    let images = [];
    const thumbnails = this.props.thumbnails;
    const thumbsLength = this.props.thumbnails.length - 1;
    const realIndex = this.state.images[slideIndex].key;
    const limitPosition = thumbsLength - realIndex; // 4 first or 4 last
    const isGreater = limitPosition < 10;
    const isLower = realIndex < 10;

    this.setState({
      slideIndex,
    });

    console.log(realIndex);
    console.log(slideIndex);

    if (slideIndex === 0 || slideIndex === 10) {
      let newSlideIndex = 1;

      if (isGreater) {
        console.log(isGreater);
        newSlideIndex = 10 - limitPosition;
        images = [
          { key: thumbsLength - 10, id: thumbnails[thumbsLength - 10].id },
          { key: thumbsLength - 9, id: thumbnails[thumbsLength - 9].id },
          { key: thumbsLength - 8, id: thumbnails[thumbsLength - 8].id },
          { key: thumbsLength - 7, id: thumbnails[thumbsLength - 7].id },
          { key: thumbsLength - 6, id: thumbnails[thumbsLength - 6].id },
          { key: thumbsLength - 5, id: thumbnails[thumbsLength - 5].id },
          { key: thumbsLength - 4, id: thumbnails[thumbsLength - 4].id },
          { key: thumbsLength - 3, id: thumbnails[thumbsLength - 3].id },
          { key: thumbsLength - 2, id: thumbnails[thumbsLength - 2].id },
          { key: thumbsLength - 1, id: thumbnails[thumbsLength - 1].id },
          { key: thumbsLength, id: thumbnails[thumbsLength].id },
        ];
      }

      if (isLower) {
        console.log('isLower');
        newSlideIndex = realIndex;
        images = [
          { key: 0, id: thumbnails[0].id },
          { key: 1, id: thumbnails[1].id },
          { key: 2, id: thumbnails[2].id },
          { key: 3, id: thumbnails[3].id },
          { key: 4, id: thumbnails[4].id },
          { key: 5, id: thumbnails[5].id },
          { key: 6, id: thumbnails[6].id },
          { key: 7, id: thumbnails[7].id },
          { key: 8, id: thumbnails[8].id },
          { key: 9, id: thumbnails[9].id },
          { key: 10, id: thumbnails[10].id },
        ];
      }

      if (!isLower && !isGreater) {
        console.log('Ambos');
        newSlideIndex = 5;
        images = [
          { key: realIndex - 5, id: thumbnails[realIndex - 5].id },
          { key: realIndex - 4, id: thumbnails[realIndex - 4].id },
          { key: realIndex - 3, id: thumbnails[realIndex - 3].id },
          { key: realIndex - 2, id: thumbnails[realIndex - 2].id },
          { key: realIndex - 1, id: thumbnails[realIndex - 1].id },
          { key: realIndex, id: thumbnails[realIndex].id },
          { key: realIndex + 1, id: thumbnails[realIndex + 1].id },
          { key: realIndex + 2, id: thumbnails[realIndex + 2].id },
          { key: realIndex + 3, id: thumbnails[realIndex + 3].id },
          { key: realIndex + 4, id: thumbnails[realIndex + 4].id },
          { key: realIndex + 5, id: thumbnails[realIndex + 5].id },
        ];
      }

      this.setState({
        images,
        slideIndex: newSlideIndex,
        speed: 0,
      }, () => {
        const elements = document.querySelectorAll('.modal-image');
        elements.forEach((el) => {
          this.hammerIt(el);
        });
        this.setState({
          speed: 280,
        });
      });
    }
  }

  goToCheckout(e) {
    if (e) e.preventDefault();
    this.props.history.push(`/client/${this.folderId}/finish-checkout`);
  }

  addRemoveFavorite(e) {
    if (e) e.preventDefault();

    const photoName = e.currentTarget.dataset.name;
    const photoId = e.currentTarget.dataset.id;
    const isFavorite = e.currentTarget.dataset.favorite;

    const data = {
      parentFolderName: this.folderId,
      photoId,
      photoName,
    };

    if (isFavorite) {
      this.removeBacketItem(data);
    } else {
      this.addBasketItem(data);
    }
  }

  addBasketItem(data) {
    return axios.post(`${apiConfig.endPoint}/cliente/cesta`, data)
      .then(() => {
        this.props.localActions.setBasketItem(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  removeBacketItem(data) {
    return axios.delete(`${apiConfig.endPoint}/cliente/cesta/${data.photoId}`)
      .then(() => {
        this.props.localActions.removeBasketItem(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  toggleShareGalleryMenu(e) {
    if (e) e.preventDefault();

    this.setState(prevState => ({
      isShareMenuBottom: !prevState.isShareMenuBottom,
    }));
  }

  render() {

    const realKeyImage = this.state.images[this.state.slideIndex].key;
    const currentImageName = this.props.thumbnails[realKeyImage].name;
    const swipe = this.state.lastScale <= 1.2;

    const isFavorite = this.props.basket[this.props.folderId]
      ? this.props.basket[this.props.folderId][this.props.thumbnails[realKeyImage].id]
        ? 'active'
        : ''
      : '';

    const images = this.state.images.length ? this.state.images.map((image) => {
      const bgStyle = { // TODO: ojo que esto hace que carguen todos los backgrounds... util si las cacheara
        background: `url(https://drive.google.com/thumbnail?authuser=0&sz=w${50}&id=${image.id}) no-repeat`,
        backgroundSize: 'contain',
        backgroundPosition: 'center',
      };

      return (
        <React.Fragment>
          <SpinnerPreloader />
          <img
            key={image.id}
            className="modal-image"
            data-img="next"
            src={`https://drive.google.com/thumbnail?authuser=0&sz=w${1000}&id=${image.id}`}
            style={bgStyle}
            alt={1}
          />
        </React.Fragment>
      );
    }) : null;

    let thumbnailComment = false;

    try {
      if (this.props.clientView.basket[this.folderId]) {
        if (this.props.clientView.basket[this.folderId][this.props.thumbnails[realKeyImage].id]) {
          if (this.props.clientView.basket[this.folderId][this.props.thumbnails[realKeyImage].id].photoComment !== '') {
            thumbnailComment = true;
          }
        }
      }
    } catch (err) {
      console.log(err);
    }

    return (
        <div className="modal-image-container">

          <div className="modal-image-top">{currentImageName}</div>
          <Carousel
            cellSpacing={120}
            withoutControls={false}
            swiping={swipe}
            vertical={false}
            renderBottomCenterControls={() => false}
            speed={this.state.speed}
            transitionMode="scroll"
            slideIndex={this.state.slideIndex}
            afterSlide={slideIndex => this.slideFinish(slideIndex)}
            renderCenterLeftControls={({ previousSlide }) => (
              <button className="button-modal-left" onClick={previousSlide}>
                <i className="fas fa-chevron-left" />
              </button>
            )}
            renderCenterRightControls={({ nextSlide }) => (
              <button className="button-modal-right" onClick={nextSlide}>
                <i className="fas fa-chevron-right" />
              </button>
            )}
          >
            {/*<img src="https://ipanematravels.com/wp-content/uploads/2018/09/Best-cities-to-visit-this-autumn-in-Europe-and-hotels-in-Europe.jpg" />*/}
            {images}
          </Carousel>

          <nav className="modal-image-bottom">

            {this.props.accessControl === 'private' && (
              <div className={`modal-add-favorites ${isFavorite}`}>
                <button
                  type="button"
                  data-id={this.props.thumbnails[realKeyImage].id}
                  data-name={this.props.thumbnails[realKeyImage].name}
                  data-favorite={isFavorite}
                  onClick={this.addRemoveFavorite}
                >
                  <i className={`fa fa-heart ${isFavorite}`} aria-hidden="true" />
                </button>
              </div>
            )}

            {(this.props.match.params.favorites === 'fav') && (
              <div className="modal-image-container__comment-button">
                <button
                  type="button"
                  onClick={this.props.showCommentModal}
                  data-photo-id={this.props.thumbnails[realKeyImage].id}
                  className={`thumbnails__comment
                  ${thumbnailComment ? ' thumbnails__comment--active' : ''}`}
                >
                  <span>
                    <i className="fas fa-comment" />
                  </span>
                  <span className="comment-tooltip">
                    Dejar un detalle
                  </span>
                </button>
              </div>
            )}

            <span className="modal-image-container__whatsapp-button">
              <i className="fab fa-whatsapp" />
            </span>

            <div className="view__sharing-buttons">

              <i className="fas fa-share" />

              <div className="modal-share-buttons">

                <ShareButtons
                  url={`https://drive.google.com/thumbnail?authuser=0&sz=w${640}&id=${this.props.thumbnails[realKeyImage].id}`}
                  urlDownload={`https://drive.google.com/uc?id=${this.props.thumbnails[realKeyImage].id}&export=download`}
                  style={{
                    left: '-50px',
                    bottom: '35px',
                  }}
                />
              </div>
            </div>

            {/*<div className="modal-image-counter">234</div>*/}

            {this.props.accessControl === 'private' && (
              <Button
                type="submit"
                btStyle="button--icon-text color-white"
                icon="far fa-arrow-alt-circle-right"
                /*label={`Enviar a ${this.props.session.userProfile.studioName}`}*/
                label="Enviar"
                onClick={this.goToCheckout}
                isLoading={this.state.isLoading}
              />
            )}
          </nav>
        </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    clientView: state.views.client,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    localActions: bindActionCreators(localActions, dispatch),
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SliderModal));
