/*
 * action types
 */
import jwtDecode from 'jwt-decode';

const LOG_IN_SUCCESS = 'LOG_IN_SUCCESS';
const SET_STUDIO_PICKER_TOKEN = 'SET_STUDIO_PICKER_TOKEN';

/*
 * action creators
 */
export function loginSuccess(token) {
  return (dispatch) => {
    const payload = jwtDecode(token);

    dispatch({
      type: LOG_IN_SUCCESS,
      userProfile: {
        email: payload.email,
        role: payload.role,
        userId: payload.userId,
        name: payload.name,
        studioName: payload.studioName,
        userName: payload.userName,
      },
      token,
      exp: payload.exp,
    });

    return Promise.resolve();
  };

  // const payload = jwtDecode(token);
  //
  // return {
  //   type: LOG_IN_SUCCESS,
  //   userProfile: {
  //     email: payload.email,
  //     role: payload.role,
  //     userId: payload.userId,
  //   },
  //   token,
  //   exp: payload.exp,
  // };
}

export function setPickerToken(token) {
  return {
    type: SET_STUDIO_PICKER_TOKEN,
    pickerToken: token,
  };
}
