import React from 'react';
import Notification from 'commonComponents/Notification';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as globalActions from 'state/actions';
// import * as globalDefinitions from 'state/actions';
import Loader from 'commonComponents/Loader';
import axios from 'axios';
import apiConfig from 'config/api';

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.getDashboardData = this.getDashboardData.bind(this);

    this.state = {
      registeredStudios: '',
    };

    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;
  }

  componentDidMount() {
    this.getDashboardData();
  }

  getDashboardData() {
    axios.get('/admin/dashboard')
      .then((res) => {
        this.setState({
          registeredStudios: res.data.registeredStudios,
        });
      })
      .catch((err) => {

      });
  }

  render() {
    return (
      <section className="view view--dashboard">

        <Loader isLoading={this.props.global.isFetching} />

        {this.props.global.message
        && (
          <Notification
            type={this.props.global.notificationType}
            message={this.props.global.message}
            dispatch={this.props.dispatch}
          />
        )}

        <header className="view__heading">
          <h4>Bienvenido al dashboard</h4>
        </header>

        <div className="view__content container-fluid">
          <div className="row">
            <div className="col-sm">
              <div className="card">
                <div className="card-body">
                  <h5>Estudios registrados</h5>
                  <h3>{this.state.registeredStudios}</h3>
                </div>
              </div>
            </div>

            <div className="col-sm">
              <div className="card">
                <div className="card-body">
                  Lorem ipsum dolor sit amet
                </div>
              </div>
            </div>

            <div className="col-sm">
              <div className="card">
                <div className="card-body">
                  Lorem ipsum dolor sit amet
                </div>
              </div>
            </div>

          </div>
        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) { // A function to make use of the store's dispatch function
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Dashboard));
