import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from 'commonComponents/Form/Button';
import apiConfig from 'config/api';
import axios from 'axios';
import * as globalActions from 'state/actions';
import Loader from 'commonComponents/Loader';
import Switch from 'react-switch';
import Modal from 'commonComponents/Modal';
import './style.scss';

class List extends Component {
  constructor(props) {
    super(props);
    const token = this.props.session.token;
    axios.defaults.baseURL = apiConfig.endPoint;
    axios.defaults.headers.common['Authorization'] = token;

    this.redirectToDetail = this.redirectToDetail.bind(this);
    this.deleteStudio = this.deleteStudio.bind(this);
    this.toggleState = this.toggleState.bind(this);
    this.handleSwitch = this.handleSwitch.bind(this);
    this.updateClientStatus = this.updateClientStatus.bind(this);
    this.showConfirm = this.showConfirm.bind(this);
    this.closeConfirm = this.closeConfirm.bind(this);
    this.showMessageModal = this.showMessageModal.bind(this);
    this.closeMessageModal = this.closeMessageModal.bind(this);
    this.handleMessage = this.handleMessage.bind(this);
    this.sendMessage = this.sendMessage.bind(this);

    this.state = {
      studios: [],
      checked: false,
      isConfirmModal: false,
      isMessageModal: false,
      currentStudioId: '',
      studioMessage: '',
    };
  }

  componentDidMount() {
    this.props.globalActions.fetchStart();
    axios.get('/admin/estudios')
      .then((res) => {
        this.setState({
          studios: res.data,
        });
        this.props.globalActions.fetchFinish();
      })
      .catch((err) => {
        //console.log('Error: /admin/estudios request');
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });
  }

  handleSwitch(checked, e, id) {
    this.setState(prevState => ({
      studios: prevState.studios.map((el) => {
        if (el.studio_id === id) {
          return {
            ...el,
            status: checked, // !el.status,
          };
        }
        return el;
      }),
    }), this.updateClientStatus(id, checked));
  }

  updateClientStatus(id, status) {
    console.log(id);
    const data = {
      status: Number(status),
    };
    axios.put(`/admin/estudio/${id}`, data)
      .then((res) => {


        // this.setState({
        //   studios: res.data,
        // });
        this.props.globalActions.fetchFinish();
      })
      .catch((err) => {
        console.log(err.response);
        this.props.globalActions.fetchFinish();
      });
  }

  redirectToDetail(id) {
    this.props.history.push(`/admin/studios/${id}/detail`);
  }

  deleteStudio(id) {
    this.closeConfirm();
    this.props.globalActions.fetchStart();

    axios.delete(`/admin/estudio/${id}`)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.props.globalActions.fetchFinish();

        this.setState(prevState => ({
          studios: prevState.studios.filter(el => el.studio_id !== id),
        }));
      })
      .catch((err) => {
        this.props.globalActions.setNotification('error', err.response.data.message);
        this.props.globalActions.fetchFinish();
      });
  }

  toggleState(e) {
    if (e) e.preventDefault();
  }

  showConfirm(currentStudioId) {
    this.setState({
      isConfirmModal: true,
      currentStudioId,
    });
  }

  closeConfirm(e) {
    if (e) e.preventDefault();
    this.setState({
      isConfirmModal: false,
    });
  }

  handleMessage(e) {
    const element = e.target;
    this.setState({
      studioMessage: element.value,
    });
  }

  showMessageModal(currentStudioId) {
    this.setState({
      isMessageModal: true,
      currentStudioId,
    });
  }

  closeMessageModal(e) {
    if (e) {
      e.preventDefault();

      const element = e.target;
      if (element.className.startsWith('modal-scrim')) {
        this.setState({
          isMessageModal: false,
          studioMessage: '',
        });
      }
      return;
    }
    this.setState({
      isMessageModal: false,
      studioMessage: '',
    });
  }

  sendMessage(e) {
    if (e) e.preventDefault();

    const data = {
      id: this.state.currentStudioId,
      message: this.state.studioMessage,
    };

    axios.post('/admin/create-message', data)
      .then((res) => {
        this.props.globalActions.setNotification('success', res.data.message);
        this.closeMessageModal();
      })
      .catch((err) => {
        console.log(err);
      });
  }

  render() {
    const studioData = this.state.studios ? this.state.studios.map(el => (
      <tr key={el.studio_id}>
        <td>{el.user_name}</td>
        <td>{el.name}</td>
        <td>
          <Switch
            onChange={this.handleSwitch}
            checked={Number(el.status)}
            id={el.studio_id}
            onColor="#20c997"
            height={22}
            width={50}
          />
        </td>
        <td>{el.phone}</td>
        <td>{el.email}</td>
        <td>{el.clientsQuantity}</td>
        <th className="table-controls">
          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-search"
            actionParams={el.studio_id}
            onClick={this.redirectToDetail}
          />
          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-envelope-square"
            actionParams={el.studio_id}
            onClick={this.showMessageModal}
          />
          <Button
            btStyle="button--icon icon--blue"
            icon="fas fa-trash-alt"
            actionParams={el.studio_id}
            onClick={this.showConfirm}
          />
        </th>
      </tr>
    )) : null;

    return (
      <section className="view view--admin-studio">

        <Loader isLoading={this.props.global.isFetching} />

        {this.state.isConfirmModal && (
          <Modal>
            <h6>
              Esta seguro de realizar esta accin?
              <br />
              Los cambios no se podrn deshacer
            </h6>
            <br />
            <div style={{ textAlign: 'center' }}>
              <Button
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Aceptar"
                isLoading={this.state.isLoading}
                actionParams={this.state.currentStudioId}
                onClick={this.deleteStudio}
              />
              &nbsp; &nbsp;
              <Button
                btStyle="button--icon-text"
                icon="fas fa-ban"
                label="Cancelar"
                onClick={this.closeConfirm}
              />
            </div>
          </Modal>
        )}


        {this.state.isMessageModal && (
          <Modal closePopUp={this.closeMessageModal}>
            <form className="form-messages">
              <textarea
                className="form-control"
                placeholder="Nuevo mensaje"
                name="studioMessage"
                value={this.state.studioMessage}
                onChange={this.handleMessage}
                required
              />
              <Button
                type="submit"
                btStyle="button--icon-text"
                icon="far fa-arrow-alt-circle-right"
                label="Enviar"
                onClick={this.sendMessage}
                isLoading={this.state.isLoading}
              />
            </form>
          </Modal>
        )}

        <header className="view__heading">
          <h4>Estudios</h4>
        </header>

        <div className="view__content container-fluid">
          <div className="card">
            <div className="card-body">
              <div className="table-responsive">
                <table className="table">
                  <thead>
                    <tr>
                      <th>Username</th>
                      <th>Nombre</th>
                      <th>Estado</th>
                      <th>Telefono</th>
                      <th>Email</th>
                      <th>Usuarios registrados</th>
                      <th style={{ minWidth: '150px' }}>Controles</th>
                    </tr>
                  </thead>
                  <tbody>
                    {studioData}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

function mapStateToProps(state) {
  return {
    session: state.session,
    global: state.global,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    globalActions: bindActionCreators(globalActions, dispatch),
    dispatch,
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(List));
